import * as React from 'react';
import autobind from 'autobind-decorator';

import { BrightcovePlayerProps, BrightcovePlayerAspectRatio } from '.';
import { Button } from 'react-bootstrap';
import { PlayerPicker } from '../common';
import { BCPlayer } from '@brightcove/video-cloud-sdk';

export class BrightcovePlayerControls extends React.Component<BrightcovePlayerControlsProps, BrightcovePlayerControlsState> {
    constructor(props: BrightcovePlayerControlsProps, context?: any) {
        super(props, context);

        this.state = {
            playerFilter: ''
        };
    }

    calculatePlayerHeight(aspectRatio: BrightcovePlayerAspectRatio, playerWidth?: string, playerHeight?: string): string | undefined {
        let newPlayerHeight = playerHeight;

        if (!playerWidth) {
            return '';
        }

        switch (aspectRatio) {
            case BrightcovePlayerAspectRatio.Widescreen:
                newPlayerHeight = Math.round(9 / 16 * parseFloat(playerWidth)).toString();
                break;
            case BrightcovePlayerAspectRatio.Standard:
                newPlayerHeight = Math.round(3 / 4 * parseFloat(playerWidth)).toString();
                break;
            default:
                // Do Nothing;
                break;
        }
        return newPlayerHeight;
    }

    @autobind
    handleAutoplayChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { bcPlayerProps, onPlayerPropsChanged } = this.props;
        bcPlayerProps.autoplay = event.target.checked;
        onPlayerPropsChanged(bcPlayerProps);
    }

    @autobind
    handleMutedChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { bcPlayerProps, onPlayerPropsChanged } = this.props;
        bcPlayerProps.muted = event.target.checked;
        onPlayerPropsChanged(bcPlayerProps);
    }

    @autobind
    handlePlayerFilterChange(value: string) {
        this.setState({
            playerFilter: value
        });
    }

    @autobind
    handlePlayerChange(selectedPlayer: BCPlayer) {
        const { bcPlayerProps, onPlayerPropsChanged } = this.props;
        bcPlayerProps.playerId = selectedPlayer.id;
        onPlayerPropsChanged(bcPlayerProps);
    }

    @autobind
    handleEmbedTypeChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { bcPlayerProps, onPlayerPropsChanged, showAdvancedEmbedCustomizations } = this.props;
        bcPlayerProps.embedType = event.target.value as 'Standard' | 'Advanced';
        if (bcPlayerProps.embedType === 'Standard') {
            bcPlayerProps.sizing = 'Fixed';
        }

        if (showAdvancedEmbedCustomizations === false) {
            bcPlayerProps.sizing = 'Responsive';
        } else {
            bcPlayerProps.sizing = 'Fixed';
        }

        onPlayerPropsChanged(bcPlayerProps);
    }

    @autobind
    handleNumThumbnailsChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { bcPlayerProps, onPlayerPropsChanged } = this.props;
        if (event.target.value) {
            bcPlayerProps.playerNumThumbnails = parseInt(event.target.value, 10);
        } else {
            bcPlayerProps.playerNumThumbnails = undefined;
        }

        onPlayerPropsChanged(bcPlayerProps);
    }

    @autobind
    handleSizingChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { bcPlayerProps, onPlayerPropsChanged } = this.props;
        bcPlayerProps.sizing = event.target.value as 'Responsive' | 'Fixed';
        onPlayerPropsChanged(bcPlayerProps);
    }

    @autobind
    handleAspectRatioChange(event: React.ChangeEvent<HTMLSelectElement>) {
        const { bcPlayerProps, onPlayerPropsChanged } = this.props;
        const { playerWidth, playerHeight } = bcPlayerProps;

        bcPlayerProps.aspectRatio = event.target.value as BrightcovePlayerAspectRatio;
        if (bcPlayerProps.aspectRatio !== BrightcovePlayerAspectRatio.Custom) {
            bcPlayerProps.playerHeight = this.calculatePlayerHeight(bcPlayerProps.aspectRatio, playerWidth, playerHeight);
        } else {
            bcPlayerProps.playerHeight = '';
        }

        onPlayerPropsChanged(bcPlayerProps);
    }

    @autobind
    handleWidthChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { bcPlayerProps, onPlayerPropsChanged } = this.props;
        bcPlayerProps.playerWidth = event.target.value || '';

        if (bcPlayerProps.aspectRatio !== BrightcovePlayerAspectRatio.Custom) {
            bcPlayerProps.playerHeight = this.calculatePlayerHeight(bcPlayerProps.aspectRatio || BrightcovePlayerAspectRatio.Widescreen, bcPlayerProps.playerWidth, bcPlayerProps.playerHeight);
        } else {
            bcPlayerProps.playerHeight = undefined;
        }

        onPlayerPropsChanged(bcPlayerProps);
    }

    @autobind
    handleHeightChange(event: React.ChangeEvent<HTMLInputElement>) {
        const { bcPlayerProps, onPlayerPropsChanged } = this.props;
        bcPlayerProps.playerHeight = event.target.value || '';

        onPlayerPropsChanged(bcPlayerProps);
    }

    @autobind
    handleUnitsChange(event: React.ChangeEvent<HTMLSelectElement>) {
        const { bcPlayerProps, onPlayerPropsChanged } = this.props;
        bcPlayerProps.units = event.target.value as 'px' | 'em';

        onPlayerPropsChanged(bcPlayerProps);
    }

    render() {
        const {
            playerType,
            renderMode,
            retrieveAllPlayers,
            defaultPlayerId,
            labelClass,
            controlClass,
            formControlClass,
            bcPlayerProps,
            showEmbedCustomizations,
            showAdvancedEmbedCustomizations,
            showUnitsCustomizations
        } = this.props;
        const { playerFilter } = this.state;
        let { whClass } = this.props;

        if (!whClass) {
            whClass = 'col-md-6';
        }

        let allowFixed = true, allowResponsive = true;
        if (playerType === 'Video') {
            if (bcPlayerProps.embedType === 'Standard') {
                allowResponsive = false;
            } else {
                allowResponsive = true;
            }
        }  else {
            if (renderMode === 'Embed') {
                if (bcPlayerProps.embedType === 'Standard') {
                    allowResponsive = false;
                } else {
                    allowResponsive = true;
                }
            } else {
                if (bcPlayerProps.embedType === 'Advanced') {
                    allowResponsive = false;
                }
            }
        }

        return (
            <>
                <div className="form-group">
                    <label className={labelClass}>Options</label>
                    <div className={controlClass}>
                        <div className={`input-group ${formControlClass}`}>
                            <label className={`checkbox-inline ${formControlClass}`}>
                                <input type="checkbox" checked={bcPlayerProps.autoplay} onChange={this.handleAutoplayChange} />Autoplay
                            </label>
                            <label className={`checkbox-inline ${formControlClass}`}>
                                <input type="checkbox" checked={bcPlayerProps.muted} onChange={this.handleMutedChange} />Muted
                            </label>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Player Filter:</label>
                    <div className={controlClass}>
                        <div className="input-group">
                            <input
                                type="search"
                                placeholder="Search"
                                className="form-control input-sm"
                                value={playerFilter}
                                onChange={(e) => this.handlePlayerFilterChange(e.target.value)}
                            />
                            <div className="input-group-btn searchButton">
                                <Button bsSize="small">&nbsp;<i className="glyphicon glyphicon-search" /></Button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Player:</label>
                    <div className={controlClass}>
                        <PlayerPicker
                            className="input-sm"
                            accountId={bcPlayerProps.accountId}
                            retrieveAllPlayers={retrieveAllPlayers}
                            playerType={playerType}
                            playerFilter={playerFilter}
                            defaultPlayerId={defaultPlayerId}
                            selectedPlayerId={bcPlayerProps.playerId}
                            onChange={this.handlePlayerChange}
                        />
                    </div>
                </div>
                {showEmbedCustomizations === true &&
                    <>
                        <div className="form-group">
                            <label className={labelClass}>Embed Type</label>
                            <div className={controlClass}>
                                <div className={`input-group ${formControlClass}`}>
                                    <label className="radio-inline">
                                        <input
                                            type="radio"
                                            name="embedType"
                                            value="Standard"
                                            checked={bcPlayerProps.embedType === 'Standard'}
                                            onChange={this.handleEmbedTypeChange}
                                        /> iFrame</label>
                                    <label className="radio-inline">
                                        <input
                                            type="radio"
                                            name="embedType"
                                            value="Advanced"
                                            checked={bcPlayerProps.embedType === 'Advanced'}
                                            onChange={this.handleEmbedTypeChange}
                                        /> JavaScript</label>
                                </div>
                            </div>
                        </div>
                        {showAdvancedEmbedCustomizations === true &&
                            <>
                                {playerType === 'Playlist' &&
                                    <div className="form-group">
                                        <label className={labelClass}>Number of Thumbnails</label>
                                        <div className={controlClass}>
                                            <input className={`form-control ${formControlClass}`} type="number" placeholder="auto" disabled={bcPlayerProps.embedType !== 'Advanced'} value={bcPlayerProps.playerNumThumbnails} onChange={this.handleNumThumbnailsChange} />
                                        </div>
                                    </div>
                                }
                                <div className="form-group">
                                    <label className={labelClass}>Sizing</label>
                                    <div className={controlClass}>
                                        <div className={`input-group ${formControlClass}`}>
                                            <label className="radio-inline">
                                                <input
                                                    type="radio"
                                                    name="sizing"
                                                    value="Fixed"
                                                    disabled={!allowFixed}
                                                    checked={bcPlayerProps.sizing === 'Fixed'}
                                                    onChange={this.handleSizingChange}
                                                /> Fixed</label>
                                            <label className="radio-inline">
                                                <input
                                                    type="radio"
                                                    name="sizing"
                                                    value="Responsive"
                                                    disabled={!allowResponsive}
                                                    checked={bcPlayerProps.sizing === 'Responsive'}
                                                    onChange={this.handleSizingChange}
                                                /> Responsive</label>
                                        </div>
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label className={labelClass}>Aspect Ratio</label>
                                    <div className={controlClass}>
                                        <select className={`form-control ${formControlClass}`} value={bcPlayerProps.aspectRatio} onChange={this.handleAspectRatioChange}>
                                            <option>16:9</option>
                                            <option>4:3</option>
                                            <option>Custom</option>
                                        </select>
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label className={labelClass}>WxH</label>
                                    <div className={controlClass}>
                                        <div className={`form-group ${formControlClass} ${whClass}`}>
                                            <label className={labelClass} style={{ paddingTop: '0' }}>Player Width</label>
                                            <div className={controlClass}>
                                                <div className="input-group">
                                                    <input className={`form-control ${formControlClass}`} type="number" placeholder="auto" value={bcPlayerProps.playerWidth} onChange={this.handleWidthChange} />
                                                    <span className="input-group-addon">{bcPlayerProps.units || 'px'}</span>
                                                </div>
                                            </div>
                                        </div>
                                        <div className={`form-group ${formControlClass} ${whClass}`}>
                                            <label className={labelClass} style={{ paddingTop: '0' }}>Player Height</label>
                                            <div className={controlClass}>
                                                <div className="input-group">
                                                    <input className={`form-control ${formControlClass}`} type="number" placeholder="auto" value={bcPlayerProps.playerHeight} disabled={bcPlayerProps.aspectRatio !== BrightcovePlayerAspectRatio.Custom} onChange={this.handleHeightChange} />
                                                    <span className="input-group-addon">{bcPlayerProps.units || 'px'}</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </>
                        }
                        {showUnitsCustomizations &&
                            <div className="form-group">
                                <label className={labelClass}>Units</label>
                                <div className={controlClass}>
                                    <select className={`form-control ${formControlClass}`} value={bcPlayerProps.units} onChange={this.handleUnitsChange}>
                                        <option>px</option>
                                        <option>em</option>
                                    </select>
                                </div>
                            </div>
                        }
                    </>
                }
            </>
        );
    }
}

export interface BrightcovePlayerControlsProps {
    defaultPlayerId?: string;
    playerType: 'Video' | 'Playlist';
    renderMode: 'Full' | 'Part' | 'Embed';
    retrieveAllPlayers: (accountId: string) => Promise<BCPlayer[]>;
    labelClass?: string;
    controlClass?: string;
    formControlClass?: string;
    showEmbedCustomizations: boolean;
    showAdvancedEmbedCustomizations: boolean;
    showUnitsCustomizations?: boolean;
    whClass?: string;
    bcPlayerProps: Partial<BrightcovePlayerProps>;
    onPlayerPropsChanged: (bcPlayerProps: Partial<BrightcovePlayerProps>) => void;
}

export interface BrightcovePlayerControlsState {
    playerFilter: string;
}


