import { ConnectorContext, ConnectorValidationResult, UserRole } from '..';

import { BrightcoveAccount } from 'services';
import { find } from 'lodash';

export async function validateConfiguration(this: ConnectorContext, currentUserEmail?: string): Promise<ConnectorValidationResult> {
    const { settingsManager, spGroups } = this.spApiGateway;

    let areAccountsAvailable = false;
    let accountEmptyAccountId = [];
    let accountEmptyGroups = [];
    let missingGroups = [];
    let currentUserAvailableAccounts: Array<BrightcoveAccount> = [];
    let currentUserMaxRole: UserRole = UserRole.None;

    const brightcoveSettings = await settingsManager.loadBrightcoveSettings();

    let isProxyDefined = false;
    if (brightcoveSettings.proxySettings &&
        brightcoveSettings.proxySettings.proxyName &&
        brightcoveSettings.proxySettings.proxyUrl) {
            isProxyDefined = true;
    }

    const accounts = brightcoveSettings.brightcoveAccounts;
    areAccountsAvailable = accounts && accounts.length > 0;

    // Act as an admin if there are no accounts available.
    if (!areAccountsAvailable) {
        currentUserMaxRole = UserRole.Author;
    }

    const siteGroups = await spGroups.getSiteGroups();
    const currentUserGroups = await spGroups.getUserGroups(currentUserEmail);

    for (const account of accounts) {
        if (!account.accountAuthorsGroupName || !account.accountViewersGroupName) {
            accountEmptyGroups.push(account.title);
            continue;
        }

        if (!account.accountId) {
            accountEmptyAccountId.push(account.title);
        }

        // If we can't find either of the defined author or viewers groups in the site groups, they're missing *cue dramatic music*
        if (!find(siteGroups, { 'title': account.accountAuthorsGroupName }) ||
            !find(siteGroups, { 'title': account.accountViewersGroupName })) {
            if (missingGroups.indexOf(account.accountAuthorsGroupName) < 0) {
                missingGroups.push(account.accountAuthorsGroupName);
            }
        }

        let currentUserIsAuthor = false, currentUserIsViewer = false;

        if (find(currentUserGroups, { 'title': account.accountAuthorsGroupName })) {
            currentUserIsAuthor = true;
            if (currentUserMaxRole < UserRole.Author) {
                currentUserMaxRole = UserRole.Author;
            }
        }

        if (find(currentUserGroups, { 'title': account.accountViewersGroupName })) {
            currentUserIsViewer = true;
            if (currentUserMaxRole < UserRole.Viewer) {
                currentUserMaxRole = UserRole.Viewer;
            }
        }

        if (currentUserIsAuthor || currentUserIsViewer) {
            currentUserAvailableAccounts.push(account);
        }
    }

    const isValid = 
        isProxyDefined &&
        areAccountsAvailable &&
        accountEmptyAccountId.length <= 0 &&
        accountEmptyGroups.length <= 0 &&
        missingGroups.length <= 0;

    return {
        isValid,
        isProxyDefined,
        areAccountsAvailable,
        accountEmptyAccountId,
        accountEmptyGroups,
        missingGroups,
        currentUserAvailableAccounts,
        currentUserMaxRole,
    };
}


