import * as React from 'react';
import * as ClipboardJS from 'clipboard';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCopy } from '@fortawesome/fontawesome-free-solid';

export class CopyToClipboard extends React.Component<CopyToClipboardProps> {
    private _cb: ClipboardJS;

    componentDidMount() {
        this._cb = new ClipboardJS(`#${this.props.id || 'copyToClipboard'}`);
    }

    componentWillUnmount() {
        this._cb && this._cb.destroy();
    }

    render() {
        const id = this.props.id || 'copyToClipboard';
        return (
            <div id={id} className="btn btn-success btn-sm pull-right" data-clipboard-target="#embedCode" style={{margin: '2px'}} onClick={() => null}>
                <FontAwesomeIcon icon={faCopy} /> Copy to Clipboard
            </div>
        );
    }
}

export interface CopyToClipboardProps {
    id?: string;
}


