/**
 * Utility function to convert an object into query string parameters.
 * @param params 
 */
export function getQueryString(params: object) {
    let queryTerms = [];
    const esc = encodeURIComponent;

    for (let key of Object.keys(params)) {
        const value = params[key];
        if (value === undefined || value === null || value === '') {
            continue;
        }

        queryTerms.push(`${esc(key)}=${esc(value)}`);
    }

    queryTerms.push(`v=${(new Date()).getTime()}`);
    return queryTerms.join('&');
}

/**
 * Utility function to convert an ArrayBuffer to a string.
 * @param buffer 
 */
export function ab2str(buffer: ArrayBuffer): string {
    let result = '';
    let bytes = new Uint8Array(buffer);
    let len = bytes.byteLength;
    for (let i = 0; i < len; i++) {
        result += String.fromCharCode(bytes[i]);
    }
    return result;
}

/**
 * Utility function to convert a string to an ArrayBuffer.
 * @param str 
 */
export function str2ab(str: string): ArrayBuffer {
    let len = str.length;
    let bytes = new Uint8Array(len);
    for (let i = 0; i < len; i++) {
        bytes[i] = str.charCodeAt(i);
    }
    return bytes.buffer;
}

/**
 * Utility function to create a random string of characters of a given length.
 * @param length Desired length of the id
 */
export function makeId(length: number): string {
    let text = '';
    let possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';

    for (let i = 0; i < length; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length));
    }

    return text;
}

/**
 * Utility function to retrieve the text contents of a File using a promise.
 * @param file 
 */
export async function readFileAsJson(file: File): Promise<any> {
    const temporaryFileReader = new FileReader();

    return new Promise((resolve, reject) => {
        temporaryFileReader.onerror = () => {
            temporaryFileReader.abort();
            reject(new Error('Problem parsing input file.'));
        };

        temporaryFileReader.onload = () => {
            resolve(JSON.parse(temporaryFileReader.result));
        };
        temporaryFileReader.readAsText(file);
    });
}


