import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import Dropzone from 'react-dropzone';

import { BrightcoveSettings, defaultBrightcoveSettings, BrightcoveAccount } from 'services';
import { BrightcoveApiGateway } from '@brightcove/video-cloud-sdk';
import { SharePointApiGateway, SPEmbedUtil } from '../../services/sharepoint';
import { ConnectorContext, UserRole, ConnectorValidationResult } from '../../services/connector';
import { AccountFieldRow } from './AccountFieldRow';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IconName } from '@fortawesome/fontawesome';
import { Spinner } from '../common';
import autobind from 'autobind-decorator';
import { faDownload, faUpload } from '@fortawesome/fontawesome-free-solid';
import { makeId } from 'services';

import * as FileSaver from 'file-saver';
import { find, isEqual, cloneDeep } from 'lodash';
import * as moment from 'moment';
import { readFileAsJson } from 'services/Utils';

import './AccountManagement.css';

export class AccountManagement extends React.Component<AccountManagementProps, AccountManagementState> {
    constructor(props: AccountManagementProps, context?: any) {
        super(props, context);

        this.state = {
            isAddingAccount: false,
            isLoadingAccounts: true,
            isSavingSettings: false,
            isImportingSettings: false,
            showConnectorSettings: false,
            currentConnectorSettings: defaultBrightcoveSettings,
            connectorSettings: defaultBrightcoveSettings
        };
    }

    async componentDidMount() {

        const { connectorContext, spApiGateway, validationResult } = this.props;
        if (validationResult.accountEmptyGroups.length === 0 && (!connectorContext.currentUserRole || connectorContext.currentUserRole !== UserRole.Author)) {
            window.location.hash = '#/';
        }

        // Get the brightcove accounts stored in the host web.
        const settings = await spApiGateway.settingsManager.loadBrightcoveSettings();
        const accounts = settings.brightcoveAccounts;

        this.setState({
            isLoadingAccounts: false,
            currentConnectorSettings: cloneDeep(settings),
            connectorSettings: settings,
            accounts
        });
    }

    @autobind
    async showConnectorSettings() {
        this.setState({
            showConnectorSettings: true
        });
    }

    @autobind
    async hideConnectorSettings() {

        this.setState({
            showConnectorSettings: false
        });
    }

    @autobind
    async saveConnectorSettings() {
        const { spApiGateway } = this.props;
        const { currentConnectorSettings } = this.state;

        this.setState({
            showConnectorSettings: false,
            isSavingSettings: true
        });

        await spApiGateway.settingsManager.saveBrightcoveSettings(currentConnectorSettings);

        window.location.reload(true);
    }

    @autobind
    async exportConnectorSettings() {
        const { spApiGateway } = this.props;

        const settings = await spApiGateway.settingsManager.loadBrightcoveSettings(false);
        var blob = new Blob([JSON.stringify(settings)], { type: 'text/plain;charset=utf-8' });
        const dt = moment();
        FileSaver.saveAs(blob, `BrightcoveConnectorSettings_${dt.format('YYYY-MM-DD')}-${dt.format('hh-mm-ss')}.json`);
    }

    @autobind
    async importConnectorSettings(acceptedFiles: File[]) {
        const { connectorContext, spApiGateway } = this.props;

        if (acceptedFiles.length !== 1) {
            return;
        }

        this.setState({
            showConnectorSettings: false,
            isImportingSettings: true
        });

        const file = acceptedFiles[0];
        try {
            const settings = await readFileAsJson(file);

            console.dir(settings);

            await spApiGateway.settingsManager.saveBrightcoveSettings(settings, false);
        } catch (ex) {
            connectorContext.raiseError('The connector settings could not be imported. Ensure that the file is a vaild Brightcove Settings export file.');
            return;
        }

        window.location.reload(true);
    }

    @autobind
    async addNewAccount() {

        this.setState({
            isAddingAccount: true
        });

        const spApiGateway = this.props.spApiGateway;

        const settings = await spApiGateway.settingsManager.loadBrightcoveSettings();
        let newId = makeId(5);
        while (find(settings.brightcoveAccounts, { id: newId })) {
            newId = makeId(5);
        }

        let newAccount: BrightcoveAccount = {
            id: makeId(5),
            title: 'New Account',
            defaultPlaylistPlayerId: '',
            defaultVideoPlayerId: '',
            accountAuthorsGroupName: '',
            accountViewersGroupName: '',
            accountId: '',
            clientId: '',
            clientSecret: ''
        };

        if (!settings.brightcoveAccounts) {
            settings.brightcoveAccounts = [];
        }

        settings.brightcoveAccounts.push(newAccount);
        await spApiGateway.settingsManager.saveBrightcoveSettings(settings);

        window.location.hash = `#/account-management/view/${newAccount.id}`;
    }

    @autobind
    async downloadEmbedHelper() {
        const { connectorContext } = this.props;
        const embedHelperBlob = SPEmbedUtil.generateEmbedHelper(connectorContext.spAddinContext.SPHostUrl, connectorContext.spAddinContext.SPAppWebUrl);
        FileSaver.saveAs(embedHelperBlob, SPEmbedUtil.getEmbedHelperFileNameFromAppWebUrl(connectorContext.spAddinContext.SPAppWebUrl));
    }

    render() {
        const plusIconName: IconName = 'plus-circle';
        const cogIconName: IconName = 'cog';

        const {
            isAddingAccount,
            isLoadingAccounts,
            isSavingSettings,
            isImportingSettings,
            accounts,
            showConnectorSettings,
            currentConnectorSettings,
            connectorSettings
        } = this.state;

        let dropzoneRef: Dropzone | null;

        if (isAddingAccount) {
            return (
                <Spinner loadingText="Creating New Account..." />
            );
        }

        if (isSavingSettings) {
            return (
                <Spinner loadingText="Saving Brightcove Settings... The app will refresh once complete." />
            );
        }

        if (isImportingSettings) {
            return (
                <Spinner loadingText="Importing Brightcove Settings... The app will refresh once complete." />
            );
        }

        if (isLoadingAccounts || !accounts) {
            return (
                <Spinner loadingText="Loading Accounts..." />
            );
        }

        const hasChanges = !isEqual(currentConnectorSettings, connectorSettings);

        return (
            <>
                <ol className="breadcrumb">
                    <li><NavLink to="/">Brightcove Management</NavLink></li>
                    <li className="active">Account Management</li>
                </ol>
                {!this.state.isLoadingAccounts &&
                    <div>
                        <table className="table table-striped">
                            <thead>
                                <tr>
                                    <td>Account Title</td>
                                    <td>Authors Group</td>
                                    <td>Viewers Group</td>
                                </tr>
                            </thead>
                            <tbody>
                                {accounts.map((account, i) =>
                                    <tr key={i}>
                                        <td>
                                            <NavLink className="mLinks btn btn-lg btn-primary" to={`/account-management/view/${account.id}`}>
                                                {account.title}
                                            </NavLink>
                                        </td>
                                        <td>
                                            <span>{account.accountAuthorsGroupName}</span>
                                        </td>
                                        <td>
                                            <span>{account.accountViewersGroupName}</span>
                                        </td>
                                    </tr>
                                )}
                            </tbody>
                        </table>
                        <hr />
                        <div>
                            <Button bsSize="large" bsStyle="default" style={{ margin: '2px' }} onClick={this.showConnectorSettings}>
                                <FontAwesomeIcon icon={cogIconName} />&nbsp;Connector Settings
                            </Button>
                            <Button bsSize="large" bsStyle="default" style={{ margin: '2px' }} onClick={this.addNewAccount}>
                                <FontAwesomeIcon icon={plusIconName} />&nbsp;Add new account
                            </Button>
                            {(process.env.NODE_ENV === 'development' || process.env.NODE_ENV === 'production') &&
                                <Button bsSize="large" bsStyle="default" style={{ margin: '2px' }} onClick={this.downloadEmbedHelper}>
                                    <FontAwesomeIcon icon={faDownload} />&nbsp;Download Embed Helper
                                </Button>
                            }
                            <hr />
                        </div>
                    </div>
                }
                <Modal show={showConnectorSettings} onHide={this.hideConnectorSettings}>
                    <Modal.Header closeButton={true}>
                        <Modal.Title>Brightcove Connector Settings</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <table className="table table-striped">
                            <thead>
                                <tr>
                                    <td>Property</td>
                                    <td>Value</td>
                                    <td>Description</td>
                                </tr>
                            </thead>
                            <tbody>
                                <AccountFieldRow
                                    fieldName="Proxy Name"
                                    fieldDescription="This field is used to specify the name of the proxy"
                                    fieldValue={currentConnectorSettings.proxySettings.proxyName}
                                    updateFieldValue={(newValue) => { this.setState({ currentConnectorSettings: { ...currentConnectorSettings, proxySettings: { ...currentConnectorSettings.proxySettings, proxyName: newValue } } }); }}
                                />
                                <AccountFieldRow
                                    fieldName="Proxy Url"
                                    fieldDescription="This field is used to specify the full address of the proxy"
                                    fieldValue={currentConnectorSettings.proxySettings.proxyUrl}
                                    updateFieldValue={(newValue) => { this.setState({ currentConnectorSettings: { ...currentConnectorSettings, proxySettings: { ...currentConnectorSettings.proxySettings, proxyUrl: newValue } } }); }}
                                />
                            </tbody>
                        </table>
                    </Modal.Body>
                    <Modal.Footer>
                        <Dropzone ref={(node) => { dropzoneRef = node; }} onDrop={this.importConnectorSettings} disableClick={true} multiple={false} style={{ textAlign: 'center', marginTop: '5px', display: 'none' }} />
                        <Button className="pull-left" onClick={this.exportConnectorSettings} bsStyle="default">
                            <FontAwesomeIcon icon={faDownload} />&nbsp;Export Settings
                        </Button>
                        <Button className="pull-left" onClick={() => { dropzoneRef && dropzoneRef.open(); }} bsStyle="default">
                            <FontAwesomeIcon icon={faUpload} />&nbsp;Import Settings
                        </Button>
                        <Button onClick={this.hideConnectorSettings} bsStyle="default">Close</Button>
                        <Button onClick={this.saveConnectorSettings} bsStyle="primary" disabled={!hasChanges}>Save</Button>
                    </Modal.Footer>
                </Modal>
            </>
        );
    }
}

interface AccountManagementProps {
    connectorContext: ConnectorContext;
    bcApiGateway: BrightcoveApiGateway;
    spApiGateway: SharePointApiGateway;
    validationResult: ConnectorValidationResult;
}

interface AccountManagementState {
    isAddingAccount: boolean;
    isLoadingAccounts: boolean;
    isSavingSettings: boolean;
    isImportingSettings: boolean;
    showConnectorSettings: boolean;
    currentConnectorSettings: BrightcoveSettings;
    connectorSettings: BrightcoveSettings;
    accounts?: Array<BrightcoveAccount>;
}



