import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { Button } from 'react-bootstrap';
import { CopyToClipboard } from '../common/CopyToClipboard';
import autobind from 'autobind-decorator';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload, faUpload } from '@fortawesome/fontawesome-free-solid';

import { BrightcoveExperience, BrightcoveExperienceProps, BrightcoveExperienceControls } from '../brightcove-experience';

import { BCExperience } from '@brightcove/video-cloud-sdk';
import { ConnectorContext } from '../../services/connector';
import { SPEmbedUtil, SPUtility } from 'services/sharepoint';
import { EmbedLayoutMode } from './EmbedLayoutMode';

import * as FileSaver from 'file-saver';
import { Spinner } from '../common';
import * as moment from 'moment';

export class ExperienceEmbed extends React.Component<ExperienceEmbedProps, ExperienceEmbedState> {
    constructor(props: ExperienceEmbedProps, context?: any) {
        super(props, context);

        this.state = {
            isWebPartUploading: false,
        };
    }

    @autobind
    handleExperiencePropsChanged(bcExperienceProps: Partial<BrightcoveExperienceProps>) {
        const { onExperiencePropsChanged } = this.props;

        bcExperienceProps.embedCodeOverride = undefined;

        if (onExperiencePropsChanged) {
            onExperiencePropsChanged(bcExperienceProps);
        }
    }

    @autobind
    handleEmbedCodeChange(embedCodeInput: React.ChangeEvent<HTMLTextAreaElement>) {
        const { bcExperienceProps, onExperiencePropsChanged } = this.props;
        bcExperienceProps.embedCodeOverride = embedCodeInput.target.value;

        if (onExperiencePropsChanged) {
            onExperiencePropsChanged(bcExperienceProps);
        }
    }

    @autobind
    async addWebPartToPage() {
        const { bcExperienceProps, connectorContext } = this.props;
        const { spApiGateway } = connectorContext;

        const embedCode = bcExperienceProps.embedCodeOverride || this.generateExperienceEmbedCode();

        await spApiGateway.spEmbedUtil.insertContentEditorWebPart(
            connectorContext.spAddinContext.Referrer,
            'Brightcove Experience',
            'Brightcove Web Part to embed an experience onto a web part page.',
            embedCode || 'An error occurred.'
        );
    }

    getWebPartTitle() {
        const { bcExperienceProps, experience } = this.props;
        let webPartName = 'Brightcove Experience WebPart';

        if (!experience || !bcExperienceProps.accountId) {
            return webPartName;
        }

        if (experience.name) {

            let embedType = 'javascript';
            if (bcExperienceProps.embedType === 'Standard') {
                embedType = 'iframe';
            }

            webPartName = `${experience.name}-${bcExperienceProps.accountId}-${experience.id}-${embedType}`;
        }
        return webPartName;
    }

    @autobind
    async uploadWebPartToGallery() {
        const { bcExperienceProps, connectorContext, experience } = this.props;
        const { spApiGateway, spAddinContext } = connectorContext;

        if (!spApiGateway || !spAddinContext || !experience || !bcExperienceProps.accountId) {
            return;
        }

        this.setState({
            isWebPartUploading: true
        });

        const embedCode = bcExperienceProps.embedCodeOverride || this.generateExperienceEmbedCode();
        const webPartTitle = this.getWebPartTitle();
        const webPart = SPEmbedUtil.generateScriptEditorWebPart(webPartTitle, 'Adds a Brightcove Experience Embed onto classic and modern pages.', embedCode || 'An error occurred.');

        try {
            const dt = moment();
            const filename = `BrightcoveExperience-${dt.format('YYYY-MM-DD')}-${dt.format('hh-mm-ss')}.webpart`;
            await spApiGateway.spEmbedUtil.uploadWebPartToGallery(webPart || 'An error occurred.', filename, 'Brightcove Experiences');
        } catch (ex) {
            // Do Nothing.
        }

        SPUtility.closeParentDialog(true);
    }

    @autobind
    downloadScriptEditorWebPart() {
        const { bcExperienceProps, experience } = this.props;

        if (!experience || !bcExperienceProps.accountId) {
            return;
        }

        const embedCode = bcExperienceProps.embedCodeOverride || this.generateExperienceEmbedCode();
        const webPartTitle = this.getWebPartTitle();
        const webPart = SPEmbedUtil.generateScriptEditorWebPart(webPartTitle, 'Adds a Brightcove Experience Embed onto classic and modern pages.', embedCode || 'An error occurred.');

        const dt = moment();
        const filename = `BrightcoveExperience-${dt.format('YYYY-MM-DD')}-${dt.format('hh-mm-ss')}.webpart`;
        FileSaver.saveAs(new Blob([webPart], { type: 'text/plain;charset=utf-8' }), filename);
    }

    generateExperienceEmbedCode() {
        const { bcExperienceProps, experience } = this.props;

        if (!bcExperienceProps.accountId || !experience) {
            return;
        }

        const embedCode = ReactDOMServer.renderToStaticMarkup(
            <BrightcoveExperience {...bcExperienceProps as BrightcoveExperienceProps} accountId={bcExperienceProps.accountId} experienceId={experience.id} experience={experience} />
        ).replace(/&amp;/g, '&').replace(/%3A/g, ':');

        return embedCode;
    }

    renderMini(embedCode: string | undefined) {
        const {
            bcExperienceProps,
            connectorContext,
            showUploadWebPartButton,
            labelClass,
            controlClass,
        } = this.props;

        const { isWebPartUploading } = this.state;

        if (!bcExperienceProps.accountId) {
            return;
        }

        return (
            <>
                <BrightcoveExperienceControls
                    accountId={bcExperienceProps.accountId}
                    connectorContext={connectorContext}
                    showEmbedCustomizations={true}
                    showAdvancedEmbedCustomizations={false}
                    disableOverrideForJavaScriptEmbeds={false}
                    labelClass={labelClass}
                    controlClass={controlClass}
                    formControlClass="input-sm"
                    bcExperienceProps={bcExperienceProps}
                    onExperiencePropsChanged={this.handleExperiencePropsChanged}
                />
                <div className="form-group">
                    <label className={labelClass}>Brightcove Snippet (Paste to Script Editor web part or publishing zone):</label>
                    <div className={controlClass}>
                        <textarea id="embedCode" style={{ width: '100%', height: '85px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={this.handleEmbedCodeChange} />
                        <CopyToClipboard />
                        <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} onClick={this.downloadScriptEditorWebPart}>
                            <FontAwesomeIcon icon={faDownload} /> Download Web Part
                        </Button>
                        {isWebPartUploading &&
                            <Spinner loadingText="Uploading..." />
                        }
                        {showUploadWebPartButton === true && !isWebPartUploading &&
                            <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} disabled={!connectorContext.doesEmbedHelperExist} onClick={this.uploadWebPartToGallery} title={connectorContext.doesEmbedHelperExist ? '' : 'Unable to upload web part as the Brightcove Embed Helper is not installed.'}>
                                <FontAwesomeIcon icon={faUpload} /> Upload Web Part to Gallery
                             </Button>
                        }
                    </div>
                </div>
            </>
        );
    }

    renderPart(embedCode: string | undefined) {
        const {
            bcExperienceProps,
            connectorContext,
            labelClass,
            controlClass,
        } = this.props;

        if (!bcExperienceProps.accountId) {
            return;
        }

        return (
            <>
                <BrightcoveExperienceControls
                    accountId={bcExperienceProps.accountId}
                    connectorContext={connectorContext}
                    showEmbedCustomizations={true}
                    showAdvancedEmbedCustomizations={false}
                    disableOverrideForJavaScriptEmbeds={true}
                    labelClass={labelClass}
                    controlClass={controlClass}
                    formControlClass="input-sm"
                    bcExperienceProps={bcExperienceProps}
                    onExperiencePropsChanged={this.handleExperiencePropsChanged}
                />
                <div className="col-xs-12" style={{ padding: '0' }}>
                    <label>Brightcove Snippet (Edit to customize Experience embed code)</label>
                </div>
                <div className="col-xs-12" style={{ padding: '0' }}>
                    <textarea id="embedCode" style={{ width: '100%', height: '85px', wordBreak: 'break-all'  }} value={embedCode} spellCheck={false} onChange={this.handleEmbedCodeChange} />
                    <CopyToClipboard />
                </div>
            </>
        );
    }

    renderFull(embedCode: string | undefined) {
        const {
            bcExperienceProps,
            connectorContext,
        } = this.props;

        if (!bcExperienceProps.accountId) {
            return;
        }

        return (
            <>
                <div className="row">
                    <div className="col-md-6">
                        <h4>Copy Code</h4>
                        <div>Copy the appropriate code below and paste it into your web page or app.</div>
                        <textarea id="embedCode" style={{ width: '100%', height: '85px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={this.handleEmbedCodeChange} />
                        <CopyToClipboard />
                        <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} onClick={this.downloadScriptEditorWebPart}>
                            <FontAwesomeIcon icon={faDownload} /> Download Web Part
                        </Button>
                    </div>
                    <div className="col-md-6">
                        <BrightcoveExperienceControls
                            accountId={bcExperienceProps.accountId}
                            connectorContext={connectorContext}
                            bcExperienceProps={bcExperienceProps}
                            showEmbedCustomizations={true}
                            showAdvancedEmbedCustomizations={false}
                            disableOverrideForJavaScriptEmbeds={false}
                            onExperiencePropsChanged={this.handleExperiencePropsChanged}
                        />
                    </div>
                </div>
            </>
        );
    }

    render() {
        const {
            bcExperienceProps,
            layoutMode,
            experience,
            labelClass,
            controlClass,
            hideExperienceName,
        } = this.props;

        if (!experience) {
            return (
                <div className="text-center">
                    <i>An experience has not been selected. Please select an experience above to obtain the embed code.</i>
                </div>
            );
        }

        if (!bcExperienceProps.accountId) {
            return (
                <div className="text-center">
                    <i>An account has not been selected. Please select an account above.</i>
                </div>
            );
        }

        const experienceSharableUrl = `https://players.brightcove.net/${bcExperienceProps.accountId}/experience_${experience.id}/share.html`;
        const embedCode = bcExperienceProps.embedCodeOverride || this.generateExperienceEmbedCode();

        let layoutSize = 'Responsive';
        if (experience.width) {
            layoutSize = `Fixed width of ${experience.width}px`;
        }

        if (experience.height) {
            layoutSize += ` and height of ${experience.height}px`;
        }

        return (
            <>
                {hideExperienceName !== true &&
                    <div className="form-group">
                        <label className={labelClass}>Experience Name</label>
                        <div className={controlClass}>{experience.name}</div>
                    </div>
                }
                {layoutMode === 'Full' &&
                    <div className="form-group">
                        <div>
                            <iframe style={{ width: '100%', height: '400px' }} src={experience.previewUrl} />
                        </div>
                    </div>
                }
                <div className="form-group">
                    <label className={labelClass}>Shareable URL</label>
                    <div className={controlClass}><a href={experienceSharableUrl} target="_blank">{experienceSharableUrl}</a></div>
                </div>
                <div className="form-group">
                    <label className={labelClass}>Experience-defined Layout Size</label>
                    <div className={controlClass}>{layoutSize}</div>
                </div>
                {(() => {
                    switch (layoutMode) {
                        case 'Mini':
                            return this.renderMini(embedCode);
                        case 'Part':
                            return this.renderPart(embedCode);
                        default:
                            return this.renderFull(embedCode);
                    }
                })()}
            </>
        );
    }
}

export interface ExperienceEmbedProps {
    bcExperienceProps: Partial<BrightcoveExperienceProps>;
    connectorContext: ConnectorContext;
    experience?: BCExperience;
    layoutMode: EmbedLayoutMode;
    hideExperienceName?: boolean;
    showUploadWebPartButton?: boolean;
    labelClass?: string;
    controlClass?: string;

    onExperiencePropsChanged?: (bcExperienceProps: Partial<BrightcoveExperienceProps>) => void;
}

export interface ExperienceEmbedState {
    isWebPartUploading: boolean;
}


