import { Web } from 'sp-pnp-js';
import { defaultsDeep } from 'lodash';

import * as URI from 'urijs';

export class SPWebPartConfig {
    private readonly _docLibPath = 'BrightcoveWebPartData/';
    private readonly _webUrl: string;
    private readonly _web: Web;

    constructor(hostWebUrl: string, addinWebUrl?: string) {
        // Use the addin web if it exists. This is different than elsewhere.
        if (!addinWebUrl) {
            this._webUrl = hostWebUrl;
            this._web = new Web(hostWebUrl);
        } else {
            this._webUrl = addinWebUrl;
            this._web = new Web(addinWebUrl);
        }
    }

    public async getBrightcoveWebPartProps<T>(webPartId: string, defaultProps: Partial<T>): Promise<Partial<T>> {
        const webPartConfigServerRelativeUrl = URI(this._webUrl + `/${this._docLibPath}${webPartId}.txt`).path();

        let props: Partial<T> = defaultProps;
        try {
            const savedProps = await this._web.getFileByServerRelativeUrl(webPartConfigServerRelativeUrl).getJSON();
            if (!savedProps.error) {
                props = defaultsDeep(savedProps, defaultProps);
            }
        } catch (ex) {
            // Do Nothing;
        }
        return props;
    }

    public async setBrightcoveWebPartProps<T>(webPartId: string, props: Partial<T>): Promise<void> {
        const webPartConfigPath = URI(this._webUrl + `/${this._docLibPath}`).path();

        try {
            this._web.getFolderByServerRelativeUrl(webPartConfigPath).files.add(`${webPartId}.txt`, JSON.stringify(props), true);
        } catch (ex) {
            // Do Nothing;
        }
    }
}


