import * as React from 'react';
import autobind from 'autobind-decorator';
import * as URI from 'urijs';

export class BrightcovePlayer extends React.Component<BrightcovePlayerProps> {
    private _scriptElement: HTMLScriptElement | null;
    private _embedElement: HTMLDivElement | null;

    componentDidMount() {
        const { embedCodeOverride } = this.props;
        if (this._embedElement && embedCodeOverride) {
            const range = document.createRange();
            range.selectNodeContents(this._embedElement);
            range.deleteContents();
            this._embedElement.appendChild(range.createContextualFragment(embedCodeOverride));
            return;
        }

        if (this._scriptElement && this._scriptElement.parentElement) {
            const parentElement = this._scriptElement.parentElement;

            const newScriptElement = document.createElement('script');
            newScriptElement.src = this._scriptElement.src;
            parentElement.removeChild(this._scriptElement);
            parentElement.appendChild(newScriptElement);
        }
    }

    @autobind
    getVideoProps(): any {
        const { accountId, mode, modeId, playerWidth, playerHeight, autoplay, muted } = this.props;
        let { playerId, units } = this.props;
        playerId = playerId || 'default';
        units = units || 'px';

        let videoProps: any = {};

        switch (mode) {
            case 'Playlist':
                videoProps['data-playlist-id'] = modeId;
                break;
            case 'Video':
                videoProps['data-video-id'] = modeId;
                break;
            default:
                throw new Error(`Unsupported mode: ${mode}`);
        }

        videoProps = {
            ...videoProps,
            'data-account': accountId,
            'data-player': playerId,
            'data-embed': 'default',
            'data-usage': `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:javascript`,
            className: 'video-js',
            controls: true,
            style: {
                width: playerWidth ? `${playerWidth}${units}` : undefined,
                height: playerHeight ? `${playerHeight}${units}` : undefined,
                position: 'relative',
                top: '0px', bottom: '0px',
                right: '0px', left: '0px',
            }
        };

        if (autoplay === true) {
            videoProps.autoPlay = autoplay;
        }

        if (muted === true) {
            videoProps.muted = muted;
        }

        switch (mode) {
            case 'Playlist':
                break;
            case 'Video':
                videoProps.style.position = 'absolute';
                break;
            default:
                throw new Error(`Unsupported mode: ${mode}`);
        }

        return videoProps;
    }

    renderIFrameFixed() {
        const { accountId, mode, modeId, playerWidth, playerHeight, autoplay, muted, renderMode } = this.props;
        let { playerId, units } = this.props;
        playerId = playerId || 'default';
        units = units || 'px';

        if (!playerWidth || !playerHeight) {
            return (
                <>Please enter correct values for width/height</>
            );
        }

        const iFrameUri = new URI(`${window.location.protocol}//players.brightcove.net/${accountId}/${playerId}_default/index.html`);
        switch (mode) {
            case 'Video':
                iFrameUri.addQuery('videoId', modeId);
                break;
            case 'Playlist':
                iFrameUri.addQuery('playlistId', modeId);
                break;
            default:
                throw new Error('Unknown Video Mode');
        }

        iFrameUri.addQuery('usage', `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:iframe`);

        if (autoplay === true) {
            iFrameUri.addQuery('autoplay');
        }

        if (muted === true) {
            iFrameUri.addQuery('muted');
        }

        const iframeUrl = iFrameUri.toString();

        const outerStyle: React.CSSProperties = {
            display: 'block',
            position: 'relative',
        };

        if (playerWidth) {
            outerStyle.width = `${playerWidth}${units}`;
        }

        if (playerHeight) {
            outerStyle.height = `${playerHeight}${units}`;
        }

        const iframeStyle: React.CSSProperties = {
            width: '100%',
            height: '100%',
            position: 'absolute',
            top: '0px',
            bottom: '0px',
            right: '0px',
            left: '0px',
        };

        if (mode === 'Playlist' || (mode === 'Video' && renderMode === 'Embed')) {
            return (
                <div style={outerStyle}>
                    <iframe
                        src={iframeUrl}
                        style={iframeStyle}
                        allowFullScreen={true}
                        // @ts-ignore
                        webkitallowfullscreen=""
                        // @ts-ignore
                        mozallowfullscreen=""
                    />
                </div>
            );
        }

        return (
            <iframe
                src={iframeUrl}
                style={iframeStyle}
                allowFullScreen={true}
                // @ts-ignore
                webkitallowfullscreen=""
                // @ts-ignore
                mozallowfullscreen=""
            />
        );
    }

    renderIFrameResponsive() {
        const { accountId, mode, modeId, playerWidth, playerHeight, autoplay, muted } = this.props;
        let { playerId, units } = this.props;
        playerId = playerId || 'default';
        units = units || 'px';

        if (!playerWidth || !playerHeight) {
            return (
                <>Please enter correct values for width/height</>
            );
        }

        const iFrameUri = new URI(`${window.location.protocol}//players.brightcove.net/${accountId}/${playerId}_default/index.html`);
        switch (mode) {
            case 'Video':
                iFrameUri.addQuery('videoId', modeId);
                break;
            case 'Playlist':
                iFrameUri.addQuery('playlistId', modeId);
                break;
            default:
                throw new Error('Unknown Video Mode');
        }

        iFrameUri.addQuery('usage', `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:iframe`);

        if (autoplay === true) {
            iFrameUri.addQuery('autoplay');
        }

        if (muted === true) {
            iFrameUri.addQuery('muted');
        }

        const iframeUrl = iFrameUri.toString();

        const outerStyle: React.CSSProperties = {
            display: 'block',
            position: 'relative',
        };

        if (playerWidth) {
            outerStyle.width = `${playerWidth}${units}`;
        }

        if (playerHeight) {
            outerStyle.height = `${playerHeight}${units}`;
        }

        const iframeStyle: React.CSSProperties = {
            width: '100%',
            height: '100%',
            position: 'absolute',
            top: '0px',
            bottom: '0px',
            right: '0px',
            left: '0px',
        };

        return (
            <iframe
                src={iframeUrl}
                style={iframeStyle}
                allowFullScreen={true}
                // @ts-ignore
                webkitallowfullscreen=""
                // @ts-ignore
                mozallowfullscreen=""
            />
        );
    }

    renderJavaScriptFixed() {
        const { accountId, mode, playerWidth, playerHeight, playerNumThumbnails } = this.props;
        let { playerId, units } = this.props;
        playerId = playerId || 'default';
        units = units || 'px';

        if (!playerWidth || !playerHeight) {
            return (
                <>Please enter correct values for width/height</>
            );
        }

        const playlistItemWidth = Math.round((parseFloat(playerWidth) - (playerNumThumbnails || 1) * 4) / (playerNumThumbnails || 1));
        const playlistItemHeight = Math.round(playlistItemWidth * parseFloat(playerHeight || '0') / parseFloat(playerWidth || '0'));

        let playerPaddingTop: number | undefined = undefined;
        if (playerHeight && playerWidth) {
            playerPaddingTop = parseFloat(playerHeight) / parseFloat(playerWidth) * 100;
        }

        const scriptUrl = `//players.brightcove.net/${accountId}/${playerId}_default/index.min.js`;
        const videoProps = this.getVideoProps();

        const styleTag = (
            <style type="text/css"> {`
.vjs-playlist {
    width: ${playerWidth}${units};
    height: calc(${playlistItemHeight}${units} + 16px);
    text-align: center;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    margin: 0;
    padding: 0;
}
.vjs-playlist-title-container {
    color: #FFFFFF;
    /*display: none;*/
    opacity: 1;
    font-size: 0.7em;
    font-family: sans-serif;
    font-weight: bold;
}
.vjs-playlist-now-playing-text {
    color: #FFFFFF;
    /*display: none;*
    opacity: 1;
    font-size: 0.7em;
    font-family: sans-serif;
    font-weight: bold;
}
.vjs-up-next-text {
    color: #FFFFFF;
    /*display: none;*/
    opacity: 1;
    font-family: sans-serif;
    font-weight: bold;
    text-align: right;
}
.vjs-playlist-duration {
    color: #FFFFFF;
    /*display: none;*/
    opacity: 1;
    font-family: sans-serif;
    font-weight: bold;
}
.vjs-mouse.vjs-playlist {
    background-color: #000000;
}
li.vjs-playlist-item {
    background-color: #000000;
    height: ${playlistItemHeight}${units};
    width: ${playlistItemWidth}${units};
    display: inline-block;
    border: 2px solid #000000;
    padding: 0;
    margin: 0;
    cursor: pointer;
    vertical-align: middle;
}
li.vjs-playlist-item:hover {
    border-color: #FFFFFF;
}`}
            </style>
        );

        const outerStyle: React.CSSProperties = {
            display: 'block',
            position: 'relative',
            width: `${playerWidth}${units}`,
        };

        if (mode === 'Playlist') {
            outerStyle.height = `calc(${playerHeight}${units} + ${playlistItemHeight}${units} + 16px)`;
        } else {
            outerStyle.height = `${playerHeight}${units}`;
        }

        return (
            <>
                {mode === 'Playlist' &&
                    styleTag
                }
                <div style={outerStyle}>
                    {mode === 'Playlist' &&
                        <>
                            <video {...videoProps} />
                            <script ref={(el) => this._scriptElement = el} src={scriptUrl} />
                            <ol className="vjs-playlist vjs-csspointerevents vjs-mouse" />
                        </>
                    }
                    {mode === 'Video' &&
                        <div style={{ paddingTop: playerPaddingTop ? `${playerPaddingTop}%` : undefined }}>
                            <video {...videoProps} />
                            <script ref={(el) => this._scriptElement = el} src={scriptUrl} />
                        </div>
                    }
                </div>
            </>
        );
    }

    renderJavaScriptResponsive() {
        const { accountId, playerWidth, playerHeight, bypassPlayerPadding, mode, renderMode } = this.props;
        let { playerId, units } = this.props;
        playerId = playerId || 'default';
        units = units || 'px';

        let playerPaddingTop: number | undefined;

        if (mode === 'Playlist' && bypassPlayerPadding === true) {
            playerPaddingTop = 0;
        } else {
            if (!playerWidth || !playerHeight) {
                return (
                    <>Please enter correct values for width/height</>
                );
            }

            if (playerHeight && playerWidth) {
                playerPaddingTop = parseFloat(playerHeight) / parseFloat(playerWidth) * 100;
            }
        }

        const scriptUrl = `//players.brightcove.net/${accountId}/${playerId}_default/index.min.js`;
        const videoProps = this.getVideoProps();
        videoProps.style.width = '100%';
        videoProps.style.height = '100%';

        let divStyle = {};
        // Non-Embed mode only, don't include a min/max width. (for now)
        if (renderMode !== 'Embed') {
            divStyle = { display: 'block', position: 'relative' };
        } else {
            divStyle = { display: 'block', position: 'relative', minWidth: '0px', maxWidth: `${playerWidth}${units}` };
        }

        // When in Embed render mode, don't render the outer div in video mode (for now)
        if (mode === 'Video' && renderMode === 'Embed') {
            return (
                <div style={{ paddingTop: playerPaddingTop ? `${playerPaddingTop}%` : undefined }}>
                    <video {...videoProps} />
                    <script ref={(el) => this._scriptElement = el} src={scriptUrl} />
                </div>
            );
        }

        return (
            <div style={divStyle}>
                <div style={{ paddingTop: playerPaddingTop ? `${playerPaddingTop}%` : undefined }}>
                    <video {...videoProps} />
                    <script ref={(el) => this._scriptElement = el} src={scriptUrl} />
                </div>
            </div>
        );
    }

    render() {
        const { embedType, sizing, embedCodeOverride } = this.props;

        if (embedCodeOverride && embedCodeOverride.length > 0) {
            const markup = { __html: embedCodeOverride };
            return (
                <div ref={(el) => this._embedElement = el} dangerouslySetInnerHTML={markup} />
            );
        }

        switch (embedType) {
            case 'Standard':
                switch (sizing) {
                    case 'Fixed':
                        return this.renderIFrameFixed();
                    case 'Responsive':
                        return this.renderIFrameResponsive();
                    default:
                        throw new Error('Unknown or unsupported embed sizing.');
                }
            case 'Advanced':
                switch (sizing) {
                    case 'Fixed':
                        return this.renderJavaScriptFixed();
                    case 'Responsive':
                        return this.renderJavaScriptResponsive();
                    default:
                        throw new Error('Unknown or unsupported embed sizing.');
                }
            default:
                throw new Error('Unknown or supported embed type.');
        }
    }
}

export enum BrightcovePlayerAspectRatio {
    Widescreen = '16:9',
    Standard = '4:3',
    Custom = 'Custom'
}

export interface BrightcovePlayerProps {
    accountId: string;
    sharepointVersion: string;
    connectorVersion: string;
    mode: 'Video' | 'Playlist';
    modeId: string;
    playerId?: string;
    autoplay: boolean;
    muted: boolean;
    embedType: 'Standard' | 'Advanced';
    sizing: 'Responsive' | 'Fixed';
    aspectRatio: BrightcovePlayerAspectRatio;
    playerWidth?: string;
    playerHeight?: string;
    playerNumThumbnails?: number;
    units?: 'px' | 'em';
    bypassPlayerPadding?: boolean;
    embedCodeOverride?: string;
    renderMode?: 'Full' | 'Part' | 'Embed';
}


