import * as React from 'react';
import { Spinner } from '../common';

import { SharePointApiGateway } from '../../services/sharepoint';
import { ConnectorValidationResult } from '../../services/connector';

export class InstallationValidation extends React.Component<InstallationValidationProps, InstallationValidationState> {
    constructor(props: InstallationValidationProps, context?: any) {
        super(props, context);

        this.state = {
            isPerformingInstallationAction: false,
            installationActionMessage: ''
        };
    }

    render() {
        const {
            validationResult
        } = this.props;

        const {
            isPerformingInstallationAction,
            installationActionMessage
        } = this.state;

        return (
            <>
                {isPerformingInstallationAction &&
                    <Spinner loadingText={installationActionMessage || 'Configuring...'} />
                }
                {!isPerformingInstallationAction &&
                    <>
                        <h4>Welcome to the Brightcove Video Connector for SharePoint!</h4>
                        <p>We've detected a few installation steps that need to be completed:</p>
                        <ul>
                            {!validationResult.isProxyDefined &&
                                <li>
                                    <span><strong>Missing/Empty Proxy settings.</strong></span>
                                    <div>The Brightcove Proxy settings must be provided. Please provide the proxy settings in 'Brightcove Settings' under Account Management</div>
                                    <br />
                                </li>
                            }
                            {!validationResult.areAccountsAvailable &&
                                <>
                                    <li>
                                        <span><strong>No accounts defined</strong></span>
                                        <div>Please add a new account within account management.</div>
                                    </li>
                                </>
                            }
                            {validationResult.areAccountsAvailable &&
                                <>
                                    {validationResult.accountEmptyAccountId.length > 0 &&
                                        <li>
                                            <span><strong>Account Id not defined in account</strong></span>
                                            <div>The following accounts are missing the account id:</div>
                                            {validationResult.accountEmptyAccountId.map((accountName, ix) =>
                                                <div key={ix}>{accountName}</div>
                                            )}
                                            <br />
                                            <p>Please correct this by editing the account settings</p>
                                            <br />
                                        </li>
                                    }

                                    {validationResult.accountEmptyGroups.length > 0 &&
                                        <li>
                                            <span><strong>Groups not defined in account</strong></span>
                                            <div>The following accounts do not have groups defined:</div>
                                            {validationResult.accountEmptyGroups.map((accountName, ix) =>
                                                <div key={ix}>{accountName}</div>
                                            )}
                                            <br />
                                            <p>Please correct this by editing the account settings</p>
                                            <br />
                                        </li>
                                    }

                                    {validationResult.missingGroups.length > 0 &&
                                        <li>
                                            <span><strong>Missing Groups</strong></span>
                                            <div>The following groups are specified within account settings but have not been created:</div>
                                            {validationResult.missingGroups.map((missingGroup, ix) =>
                                                <div key={ix}>{missingGroup}</div>
                                            )}
                                            <br />
                                        </li>
                                    }
                                </>
                            }
                        </ul>
                        <p><i>For more information or assistance, please visit the Brightcove Video Connector documentation <a href="https://support.brightcove.com/getting-started-brightcove-video-connect-sharepoint" target="_blank"> by clicking this link</a></i></p>
                    </>
                }
            </>
        );
    }
}

export interface InstallationValidationProps {
    spApiGateway: SharePointApiGateway;
    validationResult: ConnectorValidationResult;
}

export interface InstallationValidationState {
    isPerformingInstallationAction: boolean;
    installationActionMessage?: string;
}


