import { BrightcoveAccount } from './BrightcoveAccount';

export interface BrightcoveProxySettings {
    proxyName: string;
    proxyUrl: string;
}

export interface BrightcoveSettings {
    proxySettings: BrightcoveProxySettings;
    brightcoveAccounts: Array<BrightcoveAccount>;
}

export const defaultBrightcoveSettings: BrightcoveSettings = {
    proxySettings: {
        proxyName: '',
        proxyUrl: ''
    },
    brightcoveAccounts: []
};


