import * as React from 'react';
import { BootstrapTable, TableHeaderColumn, SelectRow } from 'react-bootstrap-table';
import { AccountPicker, Spinner } from '../common';
import { BrightcovePlayerProps, BrightcovePlayerUtils } from '../brightcove-player';
import { VideoSearchControls } from '../manage-videos';

import { VideoSearchParameters, getDefaultVideoSearchParameters, SearchTarget } from '../../services/connector';

import autobind from 'autobind-decorator';

import { BrightcoveAccount } from 'services';
import { BCVideo } from '@brightcove/video-cloud-sdk';
import { ConnectorContext } from '../../services/connector';
import { VideoEmbed, EmbedLayoutMode } from '../embeds';

import * as URI from 'urijs';
import { find, filter, get } from 'lodash';

export class MiniVideos extends React.Component<MiniVideosProps, MiniVideosState> {
    constructor(props: MiniVideosProps, context?: any) {
        super(props, context);

        let searchParameters = getDefaultVideoSearchParameters();
        searchParameters.searchTarget = SearchTarget.VideoCloud;

        let bcPlayerProps = BrightcovePlayerUtils.getDefaultBrightcovePlayerProps();
        bcPlayerProps.mode = 'Video';
        bcPlayerProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

        this.state = {
            bcPlayerProps,
            searchParameters,
            isLoadingVideos: false,
            videos: [],
            defaultVideoPlayerId: '',
            showUploadWebPartButton: false,
        };
    }

    async componentWillMount() {
        const { connectorContext } = this.props;
        const { bcPlayerProps } = this.state;
        if (!bcPlayerProps.accountId && connectorContext.availableAccounts && connectorContext.availableAccounts.length > 0) {
            bcPlayerProps.accountId = connectorContext.availableAccounts[0].accountId;
        }

        const selectedAccount = find(connectorContext.availableAccounts, (account) => { return account.accountId === bcPlayerProps.accountId; });
        if (selectedAccount) {
            this.setState({
                defaultVideoPlayerId: selectedAccount.defaultVideoPlayerId
            });
        }

        this.searchForVideos(bcPlayerProps.accountId || '', this.state.searchParameters);

        this.setState({
            bcPlayerProps,
            showUploadWebPartButton: connectorContext.doesEmbedHelperExist === true,
        });
    }

    @autobind
    async searchForVideos(accountId: string, searchParams: Partial<VideoSearchParameters>): Promise<void> {
        if (!accountId) {
            return;
        }

        this.setState({
            isLoadingVideos: true
        });

        const { connectorContext } = this.props;
        const { bcApiGateway } = connectorContext;
        const videosApi = bcApiGateway.videosApi;

        const bcParams = connectorContext.getBCVideoSearchParams(searchParams);
        let videos = await videosApi.getVideos(accountId, bcParams);

        if (searchParams.folderId) {
            videos = filter(videos, { 'folder_id': searchParams.folderId });
        }

        this.setState({
            isLoadingVideos: false,
            videos
        });
    }

    @autobind
    handleAccountChange(newAccount: BrightcoveAccount) {
        const { bcPlayerProps } = this.state;

        if (bcPlayerProps.accountId !== newAccount.accountId) {
            bcPlayerProps.accountId = newAccount.accountId;
            this.setState({
                bcPlayerProps
            });
            this.searchForVideos(bcPlayerProps.accountId || '', this.state.searchParameters);
        }
    }

    @autobind
    handlePlayerEmbedCodeChanged(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        this.setState({
            bcPlayerProps
        });
    }

    @autobind
    handleSearchParametersChanged(searchParameters: Partial<VideoSearchParameters>, fieldChanged: string) {
        const { bcPlayerProps } = this.state;

        this.setState({
            searchParameters
        });

        if (fieldChanged !== 'searchTerms') {
            this.searchForVideos(bcPlayerProps.accountId || '', searchParameters);
        }
    }

    @autobind
    videoSelected(video: BCVideo, isSelected: boolean, e: any) {
        const { bcPlayerProps } = this.state;
        bcPlayerProps.embedCodeOverride = undefined;

        let selectedVideo: BCVideo | undefined;
        if (isSelected) {
            selectedVideo = video;
            bcPlayerProps.modeId = selectedVideo.id;
        }
        this.setState({
            selectedVideo,
            bcPlayerProps
        });
    }

    @autobind
    renderVideoNameCell(cell: any, video: BCVideo) {
        const { bcPlayerProps } = this.state;

        const thumbnailSrc = get(video, 'images.thumbnail.src');
        let uri = URI(`${window.location.origin}${window.location.pathname}${window.location.search}`);
        uri = uri.removeQuery('mini');
        uri = uri.removeQuery('IsDlg');
        uri.hash(`#/${bcPlayerProps.accountId}/manage-videos/${video.id}/edit/`);

        return (
            <div className="row">
                <img className="col-xs-2" src={thumbnailSrc} />
                <div className="col-xs-10">
                    <a className="small" href={uri.toString()} target="_blank">{video.name}</a>
                    <div className="small">{video.id}</div>
                </div>
            </div>
        );
    }

    render() {
        const { connectorContext } = this.props;

        const {
            bcPlayerProps,
            searchParameters,
            isLoadingVideos,
            videos,
            defaultVideoPlayerId,
            selectedVideo,
            showUploadWebPartButton,
        } = this.state;

        const labelClass = 'col-xs-3 control-label small';
        const controlClass = 'col-xs-9';

        const selectRow: SelectRow = {
            mode: 'radio',
            clickToSelect: true,
            className: 'info',
            onSelect: this.videoSelected
        };

        return (
            <div className="form-horizontal">
                <div className="form-group">
                    <label className={labelClass}>Brightcove Account:</label>
                    <div className={controlClass}>
                        <AccountPicker className="input-sm" availableAccounts={connectorContext.availableAccounts} selectedAccountId={bcPlayerProps.accountId} onChange={this.handleAccountChange} />
                    </div>
                </div>
                <VideoSearchControls
                    accountId={bcPlayerProps.accountId || ''}
                    connectorContext={connectorContext}
                    labelClass={labelClass}
                    controlClass={controlClass}
                    showSearchTarget={false}
                    formControlClass="input-sm small"
                    searchParameters={searchParameters}
                    onSearchParametersChanged={this.handleSearchParametersChanged}
                    onSearchForVideos={this.searchForVideos}
                />
                {isLoadingVideos && bcPlayerProps.accountId &&
                    <Spinner loadingText="Loading Videos..." />
                }
                {!isLoadingVideos && bcPlayerProps.accountId &&
                    <div className="well">
                        <BootstrapTable data={videos} striped={true} bordered={false} selectRow={selectRow} height="250">
                            <TableHeaderColumn dataField="name" dataFormat={this.renderVideoNameCell}>Videos</TableHeaderColumn>
                            <TableHeaderColumn dataField="id" isKey={true} hidden={true}>ID</TableHeaderColumn>
                        </BootstrapTable>
                        <hr />
                    </div>
                }
                {!isLoadingVideos && bcPlayerProps.accountId && selectedVideo &&
                    <VideoEmbed
                        layoutMode={EmbedLayoutMode.Mini}
                        bcPlayerProps={bcPlayerProps}
                        videoTitle={selectedVideo.name}
                        defaultVideoPlayerId={defaultVideoPlayerId}

                        doesEmbedHelperExist={connectorContext.doesEmbedHelperExist}
                        spAddinContext={connectorContext.spAddinContext}
                        spApiGateway={connectorContext.spApiGateway}
                        showUploadWebPartButton={showUploadWebPartButton}

                        retrieveAllPlayers={connectorContext.retrieveAllPlayers}
                        onPlayerPropsChanged={this.handlePlayerEmbedCodeChanged}
                    />
                }
            </div>
        );
    }
}

interface MiniVideosProps {
    connectorContext: ConnectorContext;
}

interface MiniVideosState {
    bcPlayerProps: Partial<BrightcovePlayerProps>;
    searchParameters: Partial<VideoSearchParameters>;

    isLoadingVideos: boolean;
    videos: Array<BCVideo>;
    defaultVideoPlayerId: string;
    selectedVideo?: BCVideo;
    showUploadWebPartButton: boolean;
}


