import * as React from 'react';
import { Link } from 'react-router-dom';
import { Button, Modal } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import { AccountPicker } from '../common';
import { PlaylistEmbed, EmbedLayoutMode } from '../embeds';
import { Spinner } from '../common';
import autobind from 'autobind-decorator';
import { PlaylistSearchControls } from '.';

import { BrightcoveAccount } from 'services';
import { BrightcoveApiGateway, BCPlaylist, BCPlaylistType } from '@brightcove/video-cloud-sdk';
import { SharePointApiGateway } from '../../services/sharepoint';
import { ConnectorContext, PlaylistSearchParameters, getDefaultPlaylistSearchParameters } from '../../services/connector';
import { BrightcovePlayerUtils, BrightcovePlayerProps } from '../brightcove-player';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IconName } from '@fortawesome/fontawesome';

import { find } from 'lodash';

import './ManagePlaylists.css';

export class ManagePlaylists extends React.Component<ManagePlaylistProps, ManagePlaylistState> {
    constructor(props: ManagePlaylistProps, context?: any) {
        super(props, context);

        this.state = {
            isAddingPlaylist: false,
            isLoadingPlaylists: true,
            showEmbedModal: false,
            searchParameters: getDefaultPlaylistSearchParameters(),
            defaultPlaylistPlayerId: '',
        };
    }

    @autobind
    async componentWillMount() {
        const { accountId, connectorContext } = this.props;

        const selectedAccount = find(connectorContext.availableAccounts, (account) => { return account.accountId === accountId; });
        if (selectedAccount) {
            this.setState({
                defaultPlaylistPlayerId: selectedAccount.defaultPlaylistPlayerId
            });
        }

        await this.searchForPlaylists(this.props.accountId, this.state.searchParameters);
    }

    @autobind
    async componentWillUpdate(nextProps: ManagePlaylistProps) {
        if (nextProps.accountId !== this.props.accountId) {
            this.searchForPlaylists(nextProps.accountId, this.state.searchParameters);
        }
    }

    @autobind
    async searchForPlaylists(accountId: string, searchParams: Partial<PlaylistSearchParameters>): Promise<void> {
        if (!accountId) {
            return;
        }

        this.setState({
            isLoadingPlaylists: true
        });

        const { connectorContext } = this.props;
        const { bcApiGateway } = this.props.connectorContext;
        const playlistsApi = bcApiGateway.playlistsApi;

        const bcParams = connectorContext.getBCPlaylistSearchParams(searchParams);
        const playlists = await playlistsApi.getPlaylists(accountId, bcParams);

        this.setState({
            isLoadingPlaylists: false,
            playlists
        });
    }

    @autobind
    async handleAccountChange(newAccount: BrightcoveAccount) {
        const { accountId, onAccountChange } = this.props;

        if (accountId !== newAccount.accountId) {

            if (onAccountChange) {
                onAccountChange(newAccount.accountId);
            }
        }
    }

    @autobind
    handleEmbedPlayerPropsChanged(embedPlayerProps: Partial<BrightcovePlayerProps>) {
        this.setState({
            embedPlayerProps
        });
    }

    @autobind
    handleSearchParametersChanged(searchParameters: Partial<PlaylistSearchParameters>, fieldChanged: string) {
        this.setState({
            searchParameters
        });

        if (fieldChanged !== 'searchTerms') {
            this.searchForPlaylists(this.props.accountId, searchParameters);
        }
    }

    @autobind
    showEmbedModal(playlistId: string, playlistTitle: string) {
        const { accountId } = this.props;

        const embedPlayerProps = BrightcovePlayerUtils.getDefaultBrightcovePlaylistProps();
        embedPlayerProps.accountId = accountId;
        embedPlayerProps.playerId = this.state.defaultPlaylistPlayerId;
        embedPlayerProps.modeId = playlistId;
        embedPlayerProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

        this.setState({
            showEmbedModal: true,
            embedPlaylistTitle: playlistTitle,
            embedPlayerProps
        });
    }

    @autobind
    hideEmbedModal() {
        this.setState({
            showEmbedModal: false,
            embedPlaylistTitle: undefined,
            embedPlayerProps: undefined,
        });
    }

    @autobind
    renderPlaylistNameCell(cell: any, playlist: BCPlaylist): React.ReactElement<any> {
        const { accountId } = this.props;
        const editPlaylistUrl = `/${accountId}/manage-playlists/${playlist.id}/edit`;
        return (
            <>
                <Link to={editPlaylistUrl}>{playlist.name}</Link>
                <br />
                {playlist.video_ids && playlist.video_ids.length > 0 &&
                    <>
                        <div style={{ fontSize: 'small' }}>Video IDs in list: </div>
                        <div style={{ fontSize: 'xx-small' }}>
                            {playlist.video_ids.map((videoId) =>
                                <span key={videoId} style={{ paddingLeft: '5px', float: 'left' }}>{videoId}</span>
                            )}
                        </div>
                    </>
                }
                {(!playlist.video_ids || playlist.video_ids.length === 0) && playlist.type === BCPlaylistType.Manual &&
                    <i>This playlist does not contain any videos.</i>
                }
                {(!playlist.video_ids || playlist.video_ids.length === 0) && playlist.type !== BCPlaylistType.Manual &&
                    <i>This playlist is a Smart Playlist</i>
                }
            </>
        );
    }

    @autobind
    renderEditLink(cell: any, playlist: BCPlaylist): React.ReactElement<any> {
        const { accountId } = this.props;
        const editPlaylistUrl = `/${accountId}/manage-playlists/${playlist.id}/edit`;

        return (
            <div className="result text-center">
                <div>
                    <Link
                        className="btn btn-link"
                        style={{ width: '70px' }}
                        to={editPlaylistUrl}
                    >
                        Edit
                    </Link>
                </div>
                <div>
                    <Button
                        bsStyle="success"
                        style={{ width: '70px' }}
                        bsSize="small"
                        onClick={() => this.showEmbedModal(playlist.id, playlist.name)}
                    >
                        Embed
                    </Button>
                </div>
            </div>
        );
    }

    render() {
        const plusIconName: IconName = 'plus-circle';

        const { accountId, connectorContext } = this.props;

        const {
            isAddingPlaylist,
            isLoadingPlaylists,
            playlists,
            defaultPlaylistPlayerId,
            searchParameters,
            showEmbedModal,
            embedPlaylistTitle,
            embedPlayerProps
        } = this.state;

        const labelClass = 'col-md-3 control-label';
        const controlClass = 'col-md-9';

        if (isAddingPlaylist) {
            return (
                <Spinner loadingText="Creating New Playlist..." />
            );
        }

        return (
            <>
                <ol className="breadcrumb">
                    <li><Link to="/">Brightcove Management</Link></li>
                    <li className="active">Manage Playlists</li>
                </ol>
                <div className="search form-horizontal">
                    <div className="form-group">
                        <label className={labelClass}>Select Account:</label>{' '}
                        <div className={controlClass}>
                            <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccountId={accountId} onChange={this.handleAccountChange} />
                        </div>
                    </div>
                </div>
                <div className="search form-horizontal">
                    {accountId &&
                        <>
                            <PlaylistSearchControls
                                accountId={accountId}
                                labelClass={labelClass}
                                controlClass={controlClass}
                                searchParameters={searchParameters}
                                onSearchParametersChanged={this.handleSearchParametersChanged}
                                onSearchForPlaylists={this.searchForPlaylists}
                            />
                            {isLoadingPlaylists &&
                                <Spinner loadingText="Loading Playlists..." />
                            }
                            {!isLoadingPlaylists && playlists &&
                                <>
                                    <BootstrapTable data={playlists} striped={true} bordered={false}>
                                        <TableHeaderColumn dataField="name" dataFormat={this.renderPlaylistNameCell} dataSort={true}>Name</TableHeaderColumn>
                                        <TableHeaderColumn dataField="description" dataSort={true}>Short Description</TableHeaderColumn>
                                        <TableHeaderColumn dataField="type" dataSort={true} dataFormat={(cell, row) => { return cell === BCPlaylistType.Manual ? 'Manual Playlist' : 'Smart Playlist'; }}>Type</TableHeaderColumn>
                                        <TableHeaderColumn dataField="id" dataSort={true} isKey={true}>ID</TableHeaderColumn>
                                        <TableHeaderColumn width="90" dataFormat={this.renderEditLink}>Edit Link</TableHeaderColumn>
                                    </BootstrapTable>
                                    <hr />
                                    <Link className="btn btn-default btn-lg" to={`/${accountId}/manage-playlists/new`}>
                                        <FontAwesomeIcon icon={plusIconName} />&nbsp;Add New Playlist
                                    </Link>
                                    <hr />
                                </>
                            }
                            <Modal className="embed" show={showEmbedModal} onHide={this.hideEmbedModal} bsSize="large">
                                <Modal.Header closeButton={true}>
                                    <Modal.Title>Embed Playlist</Modal.Title>
                                </Modal.Header>
                                <Modal.Body>
                                    {embedPlayerProps &&
                                        <PlaylistEmbed
                                            layoutMode={EmbedLayoutMode.Full}
                                            bcPlayerProps={embedPlayerProps}
                                            playlistTitle={embedPlaylistTitle || ''}
                                            defaultPlaylistPlayerId={defaultPlaylistPlayerId}

                                            doesEmbedHelperExist={connectorContext.doesEmbedHelperExist}
                                            spAddinContext={connectorContext.spAddinContext}
                                            spApiGateway={connectorContext.spApiGateway}
                                            showUploadWebPartButton={false}

                                            retrieveAllPlayers={connectorContext.retrieveAllPlayers}
                                            onPlayerPropsChanged={this.handleEmbedPlayerPropsChanged}
                                        />
                                    }
                                </Modal.Body>
                                <Modal.Footer />
                            </Modal>
                        </>
                    }
                </div>
            </>
        );
    }
}

interface ManagePlaylistProps {
    accountId: string;
    connectorContext: ConnectorContext;
    bcApiGateway: BrightcoveApiGateway;
    spApiGateway: SharePointApiGateway;
    onAccountChange?: (accountId: string) => void;
}

interface ManagePlaylistState {
    isAddingPlaylist: boolean;
    isLoadingPlaylists: boolean;
    searchParameters: Partial<PlaylistSearchParameters>;
    playlists?: Array<BCPlaylist>;
    defaultPlaylistPlayerId: string;

    showEmbedModal: boolean;
    embedPlaylistTitle?: string;
    embedPlayerProps?: Partial<BrightcovePlayerProps>;
}



