import { Web, sp } from 'sp-pnp-js';
import { SharePointPageType } from '.';
import { startsWith } from 'lodash';

import * as URI from 'urijs';
import { SPProxy } from './SPProxy';
import { escape } from 'lodash';

const getContentWebPartXml = (title: string, description: string, content: string) => {
    const webPartXml = `<?xml version="1.0" encoding="utf-8"?>
<WebPart xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns="http://schemas.microsoft.com/WebPart/v2">
    <Title>${title}</Title>
    <FrameType>Default</FrameType>
    <Description>${description}</Description>
    <IsIncluded>true</IsIncluded><ZoneID></ZoneID><PartOrder>0</PartOrder>
    <FrameState>Normal</FrameState>
    <Height />
    <Width />
    <AllowRemove>true</AllowRemove>
    <AllowZoneChange>true</AllowZoneChange>
    <AllowMinimize>true</AllowMinimize>
    <AllowConnect>true</AllowConnect>
    <AllowEdit>true</AllowEdit>
    <AllowHide>true</AllowHide>
    <IsVisible>true</IsVisible>
    <DetailLink /><HelpLink />
    <HelpMode>Modeless</HelpMode>
    <Dir>Default</Dir>
    <PartImageSmall />
    <MissingAssembly>Cannot import this Web Part.</MissingAssembly>
    <PartImageLarge>/_layouts/images/mscontl.gif</PartImageLarge>
    <IsIncludedFilter />
    <Assembly>Microsoft.SharePoint, Version=13.0.0.0, Culture=neutral, PublicKeyToken=94de0004b6e3fcc5</Assembly>
    <TypeName>Microsoft.SharePoint.WebPartPages.ContentEditorWebPart</TypeName>
    <ContentLink xmlns="http://schemas.microsoft.com/WebPart/v2/ContentEditor" />
    <Content xmlns="http://schemas.microsoft.com/WebPart/v2/ContentEditor"><![CDATA[${content}]]></Content>
    <PartStorage xmlns="http://schemas.microsoft.com/WebPart/v2/ContentEditor" />
</WebPart>`;

    return webPartXml;
};

/**
 * Contains utility methods associated with the Brightcove Embed Helper
 */
export class SPEmbedUtil {
    private readonly _addinWebUrl?: string;
    private readonly _hostWebUrl: string;
    private readonly _web: Web;

    private _doesEmbedHelperExist: boolean;

    constructor(hostWebUrl: string, addinWebUrl?: string) {

        this._addinWebUrl = addinWebUrl;
        this._hostWebUrl = hostWebUrl;

        if (!addinWebUrl) {
            this._web = new Web(hostWebUrl);
        } else {
            this._web = sp.crossDomainWeb(addinWebUrl, hostWebUrl);
        }
    }

    /**
     * Returns a value that indicates if the embed helper exists.
     */
    async doesEmbedHelperExist(): Promise<boolean> {
        if (this._doesEmbedHelperExist !== undefined) {
            return this._doesEmbedHelperExist;
        }

        const embedHelperServerRelativeUrl = URI(`${this._hostWebUrl}/Brightcove/${SPEmbedUtil.getEmbedHelperFileNameFromAppWebUrl(this._addinWebUrl || this._hostWebUrl)}`).path();

        try {
            const embedHelper = await this._web.getFileByServerRelativeUrl(embedHelperServerRelativeUrl).get();
            this._doesEmbedHelperExist = embedHelper.Exists;
        } catch (ex) {
            this._doesEmbedHelperExist = false;
        }

        return this._doesEmbedHelperExist;
    }

    async getWebPartPageType(pageUrl: string): Promise<SharePointPageType | undefined> {
        const referrer = URI(pageUrl).path();
        if (!referrer || URI(referrer).equals(document.location.origin)) {
            return undefined;
        }

        const referrerServerRelativeUrl = new URI(referrer)
            .relativeTo(this._hostWebUrl)
            .toString();

        const file = await this._web.getFileByServerRelativeUrl(referrerServerRelativeUrl)
            .listItemAllFields
            .select('ContentTypeId,PublishingPageLayout')
            .get();

        let pageType = SharePointPageType.Other;

        if (startsWith(file.ContentTypeId, '0x010108')) {
            pageType = SharePointPageType.WikiPage;
        } else if (startsWith(file.ContentTypeId, '0x010109')) {
            pageType = SharePointPageType.WikiPage;
        } else if (file.PublishingPageLayout !== undefined) {
            pageType = SharePointPageType.PublishingPage;
        }

        return pageType;
    }

    async insertContentEditorWebPart(webPartPageUrl: string, title: string, description: string, content: string): Promise<string | undefined> {
        // 1. determine the type of web part page.
        const pageType = await this.getWebPartPageType(webPartPageUrl);

        if (pageType !== SharePointPageType.WikiPage &&
            pageType !== SharePointPageType.PublishingPage &&
            pageType !== SharePointPageType.WebPartPage) {
            return;
        }

        // 2. get the proxy.
        const proxy = await SPProxy.getOrCreateProxy(`${this._hostWebUrl}/Brightcove/${SPEmbedUtil.getEmbedHelperFileNameFromAppWebUrl(this._addinWebUrl || this._hostWebUrl)}`);

        const properties: any = {
            hostWebUrl: this._hostWebUrl,
            webPartPageUrl: '/' + URI(webPartPageUrl).relativeTo(URI(this._hostWebUrl).origin()).pathname(),
            webPartXml: getContentWebPartXml(title, description, content),
            zoneId: 'wpz',
            zoneIndex: 0,
        };

        switch (pageType) {
            case SharePointPageType.WikiPage:
                properties.isWikiPage = true;
                break;
            case SharePointPageType.PublishingPage:
                properties.isPublishingPage = true;
                break;
            case SharePointPageType.WebPartPage:
                // Do Nothing
                break;
            default:
                return;
        }

        // 3. invoke the method on the proxy and return the result.
        const result = await proxy.invoke('InsertWebPart', properties);
        return result.webPartId;
    }

    async uploadWebPartToGallery(webPartXml: string, webPartFileName: string, group?: string) {

        const proxy = await SPProxy.getOrCreateProxy(`${this._hostWebUrl}/Brightcove/${SPEmbedUtil.getEmbedHelperFileNameFromAppWebUrl(this._addinWebUrl || this._hostWebUrl)}`);

        const properties: any = {
            hostWebUrl: this._hostWebUrl,
            webPartXml,
            webPartFileName,
            group
        };

        await proxy.invoke('UploadWebPartToGallery', properties);
    }

    static generateEmbedHelper(hostWebUrl: string, addInWebUrl?: string): Blob {
        if (!hostWebUrl) {
            throw Error('hostWebUrl must be specified.');
        }

        const activationPageData = require('raw-loader!../../assets/BrightcoveEmbedHelper.html') as string;
        const config = {
            responseOrigin: '*',
            trustedOriginAuthorities: [
                URI(location.href).origin(),
            ],
        };

        if (addInWebUrl) {
            config.trustedOriginAuthorities.push(URI(addInWebUrl).origin());
        }

        if (process.env.NODE_ENV === 'development') {
            config.trustedOriginAuthorities.push('http://localhost:3000');
        }

        const expression = `
            window.__brightcoveConfig = ${JSON.stringify(config, null, 4)};
            `;

        const activationPage = activationPageData.replace(/(\/\/ @\}-,--`--> Start BrightcoveConfig)([\s\S]*)(\/\/ @\}-,--`--> End BrightcoveConfig)/, `$1\n${expression}\n$3`);
        return new Blob([activationPage], { type: 'text/plain;charset=utf-8' });
    }

    static getEmbedHelperFileNameFromAppWebUrl(appWebUrl: string) {
        const uri = new URI(appWebUrl);
        return `BrightcoveEmbedHelper-${uri.hostname()}.aspx`;
    }

    static generateScriptEditorWebPart(title: string, description: string, content: string) {

        const webPartData = `<?xml version="1.0" encoding="utf-8"?>
<webParts>
    <webPart xmlns="http://schemas.microsoft.com/WebPart/v3">
        <metaData>
            <type name="Microsoft.SharePoint.WebPartPages.ScriptEditorWebPart, Microsoft.SharePoint, Version=15.0.0.0, Culture=neutral, PublicKeyToken=71e9bce111e9429c" />
            <importErrorMessage>Cannot import this Web Part.</importErrorMessage>
        </metaData>
        <data>
            <properties>
                <property name="ExportMode" type="exportmode">All</property>
                <property name="HelpUrl" type="string" />
                <property name="Hidden" type="bool">False</property>
                <property name="Description" type="string">${escape(description)}</property>
                <property name="Content" type="string">${escape(content)}</property>
                <property name="CatalogIconImageUrl" type="string" />
                <property name="Title" type="string">${escape(title)}</property>
                <property name="AllowHide" type="bool">True</property>
                <property name="AllowMinimize" type="bool">True</property>
                <property name="AllowZoneChange" type="bool">True</property>
                <property name="TitleUrl" type="string" />
                <property name="ChromeType" type="chrometype">None</property>
                <property name="AllowConnect" type="bool">True</property>
                <property name="Width" type="unit" />
                <property name="Height" type="unit" />
                <property name="HelpMode" type="helpmode">Navigate</property>
                <property name="AllowEdit" type="bool">True</property>
                <property name="TitleIconImageUrl" type="string" />
                <property name="Direction" type="direction">NotSet</property>
                <property name="AllowClose" type="bool">True</property>
                <property name="ChromeState" type="chromestate">Normal</property>
            </properties>
        </data>
    </webPart>
</webParts>`;

        return webPartData;
    }
}


