import * as React from 'react';
import { HashRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import { EditPartVideo } from './components/part-video';
import { EditPartPlaylist } from './components/part-playlist';
import { EditPartExperience } from './components/part-experience';
import { BrightcovePlayer, BrightcovePlayerProps, BrightcovePlayerUtils } from './components/brightcove-player';
import { BrightcoveExperience, BrightcoveExperienceProps, BrightcoveExperienceUtils } from 'components/brightcove-experience';

import { ConnectorContext } from './services/connector';
import * as URI from 'urijs';

export class Part extends React.Component<PartProps, PartState> {
    constructor(props: PartProps, context?: any) {
        super(props, context);

        const uri = URI();
        const info = uri.query(true) as { [key: string]: string };

        this.state = {
            isLoading: true,
            isEditing: info.editmode === '1' ? true : false,
            senderId: info.SenderId,
            webPartId: info.wpId,
            embedCodeUrl: info.embedCodeUrl,
            bcPlayerProps: BrightcovePlayerUtils.getBrightcovePlayerPropsFromQueryString(),
            bcExperienceProps: BrightcoveExperienceUtils.getBrightcoveExperiencePropsFromQueryString(),
        };
    }

    async componentWillMount() {
        const { connectorContext } = this.props;
        const { spApiGateway } = connectorContext;
        const { isEditing, webPartId, embedCodeUrl } = this.state;
        let { bcPlayerProps, bcExperienceProps } = this.state;

        this.setState({
            isLoading: true,
        });

        // Get props from file.
        if (!embedCodeUrl && webPartId) {
            bcPlayerProps = await spApiGateway.spWebPartConfig.getBrightcoveWebPartProps(webPartId, bcPlayerProps);
            bcExperienceProps = await spApiGateway.spWebPartConfig.getBrightcoveWebPartProps(webPartId, bcExperienceProps);
        }

        if (embedCodeUrl && embedCodeUrl.length > 0) {
            try {
                const response = await fetch(embedCodeUrl, {
                    method: 'GET',
                    cache: 'no-cache',
                    credentials: 'same-origin',
                    redirect: 'follow',
                });

                const embedCode = await response.text();

                bcPlayerProps.embedCodeOverride = embedCode;
                bcExperienceProps.embedCodeOverride = embedCode;
            } catch (ex) {
                // Do Nothing
            }
        }

        if (isEditing) {
            await import('./Part-Edit.css');
            await connectorContext.initialize();
        }

        this.setState({
            isLoading: false,
            bcPlayerProps,
            bcExperienceProps
        });
    }

    renderPlaylist(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        const { embedCodeUrl } = this.state;
        if ((!bcPlayerProps.accountId || !bcPlayerProps.modeId) && !embedCodeUrl) {
            return (
                <>
                    <p><strong>This Brightcove Playlist needs to be configured.</strong></p>
                    <p>Please edit this section to select the Brightcove playlist to display.</p>
                </>
            );
        }

        return (
            <BrightcovePlayer bypassPlayerPadding={true} {...bcPlayerProps as BrightcovePlayerProps} />
        );
    }

    renderVideo(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        const { embedCodeUrl } = this.state;
        if ((!bcPlayerProps.accountId || !bcPlayerProps.modeId) && !embedCodeUrl) {
            return (
                <>
                    <p><strong>This Brightcove Video needs to be configured.</strong></p>
                    <p>Please edit this section to the Brightcove video to display.</p>
                </>
            );
        }

        return (
            <BrightcovePlayer bypassPlayerPadding={true} {...bcPlayerProps as BrightcovePlayerProps} />
        );
    }

    renderExperience(bcExperienceProps: Partial<BrightcoveExperienceProps>) {
        const { embedCodeUrl } = this.state;
        if ((!bcExperienceProps.accountId || !bcExperienceProps.experienceId) && !embedCodeUrl) {
            return (
                <>
                    <p><strong>This Brightcove Experience needs to be configured.</strong></p>
                    <p>Please edit this section to select the Brightcove experience to display.</p>
                </>
            );
        }

        return (
            <BrightcoveExperience {...bcExperienceProps as BrightcoveExperienceProps} />
        );
    }

    render() {
        const { connectorContext } = this.props;
        const { isLoading, isEditing, bcPlayerProps, bcExperienceProps } = this.state;

        let partStyle: React.CSSProperties = {
        };

        if (isLoading) {
            return (
                <div className="part" style={partStyle} />
            );
        }

        return (
            <Router>
                <div className="part" style={partStyle}>
                    <Switch>
                        <Route path="/part/playlist" exact={true}>
                            <>
                                {isEditing &&
                                    <EditPartPlaylist connectorContext={connectorContext} playerProps={bcPlayerProps} />
                                }
                                {!isEditing && this.renderPlaylist(bcPlayerProps)}
                            </>
                        </Route>
                        <Route path="/part/video" exact={true}>
                            <>
                                {isEditing &&
                                    <EditPartVideo connectorContext={connectorContext} playerProps={bcPlayerProps} />
                                }
                                {!isEditing && this.renderVideo(bcPlayerProps)}
                            </>
                        </Route>
                        <Route path="/part/experience" exact={true}>
                            <>
                                {isEditing &&
                                    <EditPartExperience connectorContext={connectorContext} experienceProps={bcExperienceProps} />
                                }
                                {!isEditing && this.renderExperience(bcExperienceProps)}
                            </>
                        </Route>
                        <Redirect to="/" />
                    </Switch>
                </div>
            </Router>
        );
    }
}

interface PartProps {
    connectorContext: ConnectorContext;
    raiseError: (message: string, err?: Error) => boolean;
}

interface PartState {
    isLoading: boolean;
    isEditing: boolean;
    senderId: string;
    webPartId: string;
    embedCodeUrl?: string;
    bcPlayerProps: Partial<BrightcovePlayerProps>;
    bcExperienceProps: Partial<BrightcoveExperienceProps>;
}



