import * as React from 'react';
import { Button } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn, SelectRow } from 'react-bootstrap-table';
import { AccountPicker, Spinner } from '../common';
import { BrightcovePlayerProps } from '../brightcove-player';
import { PlaylistEmbed, EmbedLayoutMode } from '../embeds';
import { PlaylistSearchControls } from '../manage-playlists';

import { BrightcoveAccount } from 'services';
import { BCPlaylist, BCGetPlaylistsParams } from '@brightcove/video-cloud-sdk';
import { ConnectorContext, PlaylistSearchParameters, getDefaultPlaylistSearchParameters, SortOrder, PlaylistSortField } from '../../services/connector';

import autobind from 'autobind-decorator';
import * as URI from 'urijs';
import { find } from 'lodash';

export class EditPartPlaylist extends React.Component<EditPartPlayerProps, EditPartPlayerState> {
    constructor(props: EditPartPlayerProps, context?: any) {
        super(props, context);

        let bcPlayerProps = props.playerProps;
        bcPlayerProps.mode = 'Playlist';
        bcPlayerProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

        const uri = URI();
        const info = uri.query(true) as { [key: string]: string };

        this.state = {
            bcPlayerProps,
            searchParameters: getDefaultPlaylistSearchParameters(),
            isLoadingPlaylists: false,
            isSaving: false,
            playlists: [],
            defaultPlaylistPlayerId: '',
            webPartId: info.wpId,
        };
    }

    componentWillMount() {
        const { connectorContext } = this.props;
        const { bcPlayerProps } = this.state;
        if (!bcPlayerProps.accountId && connectorContext.availableAccounts && connectorContext.availableAccounts.length > 0) {
            bcPlayerProps.accountId = connectorContext.availableAccounts[0].accountId;
        }

        const selectedAccount = find(connectorContext.availableAccounts, (account) => { return account.accountId === bcPlayerProps.accountId; });
        if (selectedAccount) {
            this.setState({
                defaultPlaylistPlayerId: selectedAccount.defaultPlaylistPlayerId
            });
        }

        this.searchForPlaylists(bcPlayerProps.accountId || '', this.state.searchParameters);

        this.setState({
            bcPlayerProps,
        });
    }

    @autobind
    async searchForPlaylists(accountId: string, searchParams: Partial<PlaylistSearchParameters>): Promise<void> {
        if (!accountId) {
            return;
        }

        this.setState({
            isLoadingPlaylists: true
        });

        const { bcApiGateway } = this.props.connectorContext;
        const { bcPlayerProps } = this.state;
        const playlistsApi = bcApiGateway.playlistsApi;

        // Map search params to 'q'
        let bcParams: BCGetPlaylistsParams = {
            limit: searchParams.searchLimit || 45,
            sort: searchParams.sortOrder === SortOrder.Descending ? '-' : '',
            q: searchParams.searchTerms ? `+"${searchParams.searchTerms}"` : ''
        };

        switch (searchParams.sortField) {
            case PlaylistSortField.DisplayName:
                bcParams.sort += 'name';
                break;
            case PlaylistSortField.ModifiedDate:
                bcParams.sort += 'updated_at';
                break;
            default:
                // Do nothing.
                break;
        }

        const playlists = await playlistsApi.getPlaylists(accountId, bcParams);

        let selectedPlaylist: BCPlaylist | undefined;
        if (bcPlayerProps.modeId) {
            selectedPlaylist = find(playlists, { 'id': bcPlayerProps.modeId });
        }

        this.setState({
            isLoadingPlaylists: false,
            selectedPlaylist,
            playlists
        });
    }

    @autobind
    async handleAccountChange(newAccount: BrightcoveAccount) {
        const { bcPlayerProps } = this.state;

        if (bcPlayerProps.accountId !== newAccount.accountId) {
            bcPlayerProps.accountId = newAccount.accountId;
            this.setState({
                bcPlayerProps
            });
            this.searchForPlaylists(bcPlayerProps.accountId || '', this.state.searchParameters);
        }
    }

    @autobind
    handleSearchParametersChanged(searchParameters: Partial<PlaylistSearchParameters>, fieldChanged: string) {
        this.setState({
            searchParameters
        });

        if (fieldChanged !== 'searchTerms') {
            this.searchForPlaylists(this.state.bcPlayerProps.accountId || '', searchParameters);
        }
    }

    @autobind
    handlePlayerPropsChanged(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        this.setState({
            bcPlayerProps
        });
    }

    @autobind
    playlistSelected(playlist: BCPlaylist, isSelected: boolean, e: any) {
        const { bcPlayerProps } = this.state;
        bcPlayerProps.embedCodeOverride = undefined;

        let selectedPlaylist: BCPlaylist | undefined;
        if (isSelected) {
            selectedPlaylist = playlist;
            bcPlayerProps.modeId = selectedPlaylist.id;
        }

        this.setState({
            selectedPlaylist,
            bcPlayerProps,
        });
    }

    @autobind
    renderPlaylistNameCell(cell: any, playlist: BCPlaylist) {
        const { bcPlayerProps } = this.state;

        let uri = URI(`${window.location.origin}${window.location.pathname.replace(/AppPartPage.aspx/i, 'Default.aspx')}${window.location.search}`);
        uri = uri.removeQuery('part');
        uri = uri.removeQuery('IsDlg');
        uri.hash(`/${bcPlayerProps.accountId}/manage-playlists/${playlist.id}/edit`);

        return (
            <div className="row">
                <div className="col-xs-12">
                    <a className="small" href={uri.toString()} target="_blank">{playlist.name}</a>
                    <div className="small">{playlist.id}</div>
                </div>
            </div>
        );
    }

    @autobind
    async applyWebpartChanges() {
        this.setState({
            isSaving: true
        });

        const { connectorContext } = this.props;
        const { spApiGateway } = connectorContext;
        const { bcPlayerProps, selectedPlaylist, webPartId } = this.state;

        if (!selectedPlaylist) {
            return;
        }

        bcPlayerProps.modeId = selectedPlaylist.id || '';

        await spApiGateway.spWebPartConfig.setBrightcoveWebPartProps(webPartId, bcPlayerProps);
        location.reload(true);
    }

    @autobind
    async cancelWebpartChanges() {
        location.reload(true);
    }

    render() {
        const { connectorContext } = this.props;

        const {
            bcPlayerProps,
            searchParameters,
            isLoadingPlaylists,
            isSaving,
            playlists,
            defaultPlaylistPlayerId,
            selectedPlaylist,
        } = this.state;

        const labelClass = 'col-xs-3 control-label small';
        const controlClass = 'col-xs-9';

        const selectRow: SelectRow = {
            mode: 'radio',
            clickToSelect: true,
            className: 'info',
            onSelect: this.playlistSelected
        };

        if (bcPlayerProps.modeId) {
            selectRow.selected = [bcPlayerProps.modeId];
        }

        if (selectedPlaylist) {
            selectRow.selected = [selectedPlaylist.id];
        }

        const canApply = !isLoadingPlaylists && bcPlayerProps.accountId && selectedPlaylist;

        return (
            <div className="form-horizontal">
                <div className="form-group">
                    <label className={labelClass}>Brightcove Account:</label>
                    <div className={controlClass}>
                        <AccountPicker className="input-sm" availableAccounts={connectorContext.availableAccounts} selectedAccountId={bcPlayerProps.accountId} onChange={this.handleAccountChange} />
                    </div>
                </div>
                <PlaylistSearchControls
                    accountId={bcPlayerProps.accountId || ''}
                    labelClass={labelClass}
                    controlClass={controlClass}
                    formControlClass="input-xs small"
                    searchParameters={searchParameters}
                    onSearchParametersChanged={this.handleSearchParametersChanged}
                    onSearchForPlaylists={this.searchForPlaylists}
                />
                {isLoadingPlaylists && bcPlayerProps.accountId &&
                    <Spinner loadingText="Loading Playlists..." />
                }
                {!isLoadingPlaylists && bcPlayerProps.accountId &&
                    <>
                        <div className="well" >
                            <BootstrapTable data={playlists} striped={true} bordered={false} selectRow={selectRow} height="250">
                                <TableHeaderColumn dataField="name" dataFormat={this.renderPlaylistNameCell}>Playlist</TableHeaderColumn>
                                <TableHeaderColumn dataField="id" isKey={true} hidden={true}>ID</TableHeaderColumn>
                            </BootstrapTable>
                            <hr />
                        </div>
                    </>
                }
                {!isLoadingPlaylists && bcPlayerProps.accountId && selectedPlaylist &&
                    <PlaylistEmbed
                        layoutMode={EmbedLayoutMode.Part}
                        bcPlayerProps={bcPlayerProps}
                        playlistTitle={selectedPlaylist.name}
                        defaultPlaylistPlayerId={defaultPlaylistPlayerId}

                        doesEmbedHelperExist={connectorContext.doesEmbedHelperExist}
                        spAddinContext={connectorContext.spAddinContext}
                        spApiGateway={connectorContext.spApiGateway}

                        showUploadWebPartButton={false}

                        retrieveAllPlayers={connectorContext.retrieveAllPlayers}
                        onPlayerPropsChanged={this.handlePlayerPropsChanged}
                    />
                }
                <div className="col-xs-12" style={{ paddingTop: '5px' }}>
                    {isSaving &&
                        <Spinner loadingText="Saving..." />
                    }
                    {!isSaving &&
                        <>
                            <Button className="pull-right" bsStyle="primary" disabled={!canApply} style={{ margin: '2px' }} onClick={this.applyWebpartChanges}>Apply</Button>
                            <Button className="pull-right" style={{ margin: '2px' }} onClick={this.cancelWebpartChanges}>Cancel</Button>
                        </>
                    }
                </div>
            </div>
        );
    }
}

interface EditPartPlayerProps {
    connectorContext: ConnectorContext;
    playerProps: Partial<BrightcovePlayerProps>;
}

interface EditPartPlayerState {
    bcPlayerProps: Partial<BrightcovePlayerProps>;
    searchParameters: Partial<PlaylistSearchParameters>;

    isLoadingPlaylists: boolean;
    isSaving: boolean;
    playlists: Array<BCPlaylist>;
    selectedPlaylist?: BCPlaylist;
    defaultPlaylistPlayerId: string;
    webPartId: string;
}


