import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';
import { Prompt } from 'react-router';
import { Link, NavLink } from 'react-router-dom';
import { AccountFieldRow } from './AccountFieldRow';

import { BrightcoveAccount } from 'services';
import { BCGetCustomFieldsResponse } from '@brightcove/video-cloud-sdk';
import { ConnectorContext, UserRole, ConnectorValidationResult } from '../../services/connector';

import { Spinner } from '../common';
import autobind from 'autobind-decorator';
import { isEqual, cloneDeep, find, pull, merge } from 'lodash';

import './AccountView.css';

export class AccountView extends React.Component<AccountViewProps, AccountViewState> {
    constructor(props: AccountViewProps, context?: any) {
        super(props, context);

        this.state = {
            isDeletingAccount: false,
            isSavingAccount: false,
            isLoadingAccount: true,
            isLoadingCustomFields: false,
            showCustomFieldsModal: false,
            showDeleteModal: false,
        };
    }

    async componentDidMount() {

        const { connectorContext, validationResult } = this.props;
        const { spApiGateway } = connectorContext;

        if (validationResult.accountEmptyGroups.length === 0 && (!connectorContext.currentUserRole || connectorContext.currentUserRole !== UserRole.Author)) {
            window.location.hash = '#/';
        }

        // Get the brightcove accounts stored in settings.
        const settings = await spApiGateway.settingsManager.loadBrightcoveSettings();
        const account = find(settings.brightcoveAccounts, { id: this.props.accountId });

        if (!account) {
            window.location.hash = '#/account-management';
            return;
        }

        this.setState({
            isLoadingAccount: false,
            account: cloneDeep(account),
            originalAccount: account
        });
    }

    @autobind
    async saveAccountChanges() {

        this.setState({
            isSavingAccount: true
        });

        const { account } = this.state;

        if (!account) {
            return;
        }

        const { connectorContext } = this.props;
        const { spApiGateway } = connectorContext;
        var settings = await spApiGateway.settingsManager.loadBrightcoveSettings();
        const existingAccount = find(settings.brightcoveAccounts, { id: this.props.accountId });
        merge(existingAccount, account);
        await spApiGateway.settingsManager.saveBrightcoveSettings(settings);

        // Update available accounts.
        await connectorContext.refreshAccounts();

        this.setState({
            isSavingAccount: false,
            account: cloneDeep(existingAccount),
            originalAccount: existingAccount
        });

        window.location.hash = '#/account-management';
    }

    @autobind
    deleteAccount() {
        this.setState({
            showDeleteModal: true
        });
    }

    @autobind
    deleteCancel() {
        this.setState({
            showDeleteModal: false
        });
    }

    @autobind
    async deleteConfirm() {

        this.setState({
            isDeletingAccount: true
        });

        const { connectorContext } = this.props;
        const { spApiGateway } = connectorContext;
        const settings = await spApiGateway.settingsManager.loadBrightcoveSettings();
        const existingAccount = find(settings.brightcoveAccounts, { id: this.props.accountId });
        pull(settings.brightcoveAccounts, existingAccount);
        await spApiGateway.settingsManager.saveBrightcoveSettings(settings);
        window.location.hash = '#/account-management';
    }

    render() {
        const { connectorContext } = this.props;

        const {
            isDeletingAccount,
            isSavingAccount,
            isLoadingAccount,
            account,
            originalAccount,
        } = this.state;

        if (isDeletingAccount) {
            return (
                <Spinner loadingText="Deleting Account..." />
            );
        }

        if (isSavingAccount) {
            return (
                <Spinner loadingText="Saving Account..." />
            );
        }

        if (isLoadingAccount || !account) {
            return (
                <Spinner loadingText="Loading Account..." />
            );
        }

        const hasChanges = isEqual(account, originalAccount);

        return (
            <>
                <ol className="breadcrumb">
                    <li><NavLink to="/">Brightcove Management</NavLink></li>
                    <li><NavLink to="/account-management">Account Management</NavLink></li>
                    <li className="active">{account.title}</li>
                </ol>

                <div className="hideSection">
                    <table className="table table-striped">
                        <thead>
                            <tr>
                                <td>Property</td>
                                <td>Value</td>
                                <td>Description</td>
                            </tr>
                        </thead>
                        <tbody>
                            <AccountFieldRow
                                fieldName="Account Name"
                                fieldDescription="This field is used to specify the account name"
                                fieldValue={account.title}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, title: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="DefaultVideoPlayerId"
                                fieldDescription="This field is used to specify the default video player of the account"
                                fieldValue={account.defaultVideoPlayerId}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, defaultVideoPlayerId: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="DefaultPlaylistPlayerId"
                                fieldDescription="This field is used to specify the default playlist id of the account"
                                fieldValue={account.defaultPlaylistPlayerId}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, defaultPlaylistPlayerId: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="Account Id"
                                fieldDescription="The Brightcove Account Id"
                                fieldValue={account.accountId}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, accountId: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="Client Id"
                                fieldDescription="The Brightcove API Authentication Client Id"
                                fieldValue={account.clientId}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, clientId: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="Client Secret"
                                fieldDescription="The Brightcove API Authentication Client Secret"
                                fieldValue={account.clientSecret}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, clientSecret: newValue } }); }}
                            />
                        </tbody>
                    </table>
                    <hr />
                    <table className="table table-striped">
                        <thead>
                            <tr>
                                <td>Group</td>
                                <td>Value</td>
                            </tr>
                        </thead>
                        <tbody>
                            <AccountFieldRow
                                fieldName="Author's Group"
                                fieldValue={account.accountAuthorsGroupName}
                                fieldValueType="group"
                                connectorContext={connectorContext}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, accountAuthorsGroupName: newValue } }); }}
                            />
                            <AccountFieldRow
                                fieldName="Viewer's Group"
                                fieldValue={account.accountViewersGroupName}
                                fieldValueType="group"
                                connectorContext={connectorContext}
                                updateFieldValue={(newValue) => { this.setState({ account: { ...account, accountViewersGroupName: newValue } }); }}
                            />
                        </tbody>
                    </table>
                    <div>
                        <hr />
                        <Button bsStyle={!hasChanges ? 'primary' : 'default'} bsSize="large" disabled={hasChanges} onClick={this.saveAccountChanges} style={{ margin: '2px' }}>Save Account</Button>
                        <Button bsStyle="warning" bsSize="large" onClick={this.deleteAccount} style={{ margin: '2px' }}>Delete Account</Button>
                        <Link className="btn btn-lg btn-default" to="/account-management" style={{ margin: '2px' }}>Cancel</Link>
                    </div>
                </div>

                <Modal show={this.state.showDeleteModal} onHide={this.deleteCancel}>
                    <Modal.Header closeButton={true}>
                        <Modal.Title>Confirm Account Deletion</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <h5>This account will be permanently deleted. Are you sure you want to proceed?</h5>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={this.deleteConfirm} bsStyle="danger">Delete Account</Button>
                        <Button onClick={this.deleteCancel}>Cancel</Button>
                    </Modal.Footer>
                </Modal>
                <Prompt
                    when={!hasChanges}
                    message="There are unsaved changes. Are you sure you wish to leave?"
                />
            </>
        );
    }
}

interface AccountViewProps {
    connectorContext: ConnectorContext;
    accountId: string;
    validationResult: ConnectorValidationResult;
}

interface AccountViewState {
    account?: BrightcoveAccount;
    originalAccount?: BrightcoveAccount;
    isDeletingAccount: boolean;
    isSavingAccount: boolean;
    isLoadingAccount: boolean;
    isLoadingCustomFields: boolean;
    customFields?: BCGetCustomFieldsResponse;
    accountInEdit?: BrightcoveAccount;
    showCustomFieldsModal: boolean;
    showDeleteModal: boolean;
}


