import * as React from 'react';

import { FormControl } from 'react-bootstrap';
import autobind from 'autobind-decorator';

import { BrightcoveAccount } from 'services';

import { find } from 'lodash';

export class AccountPicker extends React.Component<AccountPickerProps, AccountPickerState> {
    constructor(props: AccountPickerProps, context?: any) {
        super(props, context);

        this.state = {
            isLoadingAccounts: true,
        };
    }

    async componentDidMount() {
        const { availableAccounts, selectedAccountId, onChange } = this.props;

        this.setState({
            isLoadingAccounts: true,
        });

        if (!selectedAccountId && availableAccounts && availableAccounts.length > 0) {
            const newSelectedAccount = availableAccounts[0];
            if (onChange) {
                onChange(newSelectedAccount);
            }
        }

        this.setState({
            isLoadingAccounts: false,
        });
    }

    @autobind
    handleFieldChange(event: React.FormEvent<FormControl>) {
        const { availableAccounts, onChange } = this.props;
        const target = event.target as any;

        const selectedAccount = find(availableAccounts, (account) => { return account.accountId === target.value; });

        if (selectedAccount && onChange) {
            onChange(selectedAccount);
        }
    }

    render() {
        const { disabled, className, availableAccounts, selectedAccountId } = this.props;
        const isLoadingAccounts = false;

        if (isLoadingAccounts) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                >
                    <option>Loading Accounts...</option>
                </FormControl>
            );
        }

        if (!availableAccounts || availableAccounts.length <= 0) {
            return (
                <FormControl
                    className={className}
                    componentClass="select"
                    disabled={true}
                >
                    <option>There are no available accounts.</option>
                </FormControl>
            );
        }

        return (
            <FormControl
                className={className}
                componentClass="select"
                placeholder="Select an account..."
                value={selectedAccountId}
                onChange={this.handleFieldChange}
                disabled={disabled || false}
            >
                {availableAccounts.map((account, i) =>
                    <option key={i} value={account.accountId}>{account.title}</option>
                )
                }
            </FormControl>
        );
    }
}

interface AccountPickerProps {
    className?: string;
    availableAccounts: Array<BrightcoveAccount>;
    selectedAccountId?: string;
    onChange?: (selectedAccount: BrightcoveAccount) => void;
    disabled?: boolean;
}

interface AccountPickerState {
}


